<template>
  <select v-model="selected">
    <option v-for="(o, idx) in getText(tid)" :key="idx" :value="idx">{{o}}</option>
  </select>
</template>
<script>
export default {
  name: 'txtOption',
  props: ['tid', 'value'],
  data () {
    return {
    }
  },
  computed: {
    selected: {
      get () {
        return this.value
      },
      set (v) {
        this.$emit('input', v)
      }
    }
  },
  methods: {
    getText () {
      var text = this.tid
      var typ = this.typ
      var idx = this.idx
      if (typ === undefined || typ === '') {
        typ = 'txt'
      }
      if (idx === undefined || idx === '') {
        idx = -1
      } else {
        idx = parseInt(idx)
      }
      var tmp = ''
      if (typ in this.$texts) {
        if (text in this.$texts[typ]) {
          if (this.$texts[typ][text].length > this.$languageIdx) {
            tmp = this.$texts[typ][text][this.$languageIdx]
            console.log(this.$languageIdx, text, typ, idx, tmp)
            if (tmp.indexOf('~') > 0) {
              return (tmp.split('~'))
            }
            return (['nix'])
          }
        }
      }
      return ('$' + text)
    }

  }
}
</script>
<style scoped>
</style>


