<template>
  <div class="">
    <div v-if="activeProject">
      <div class="columns is-multiline">
        <div class="column" v-for="(images, index) in testImages.list" :key="index">
          <img :id="'testImageTarget' + index" :src="'/images/testImages/' + testImages.list[index]" @click="changeImage(testImages.list[index]);sendCmd()">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import bus from '@/components/bus'

export default {
  name: 'imgPicker',
  components: {
  },
  props: ['activePlugin', 'pluginConfig', 'activeProject'],
  data () {
    return {
      testImages: []
    }
  },
  methods: {
    getTestImages () {
      axios.get('/api/v1/Project/listFiles?path=images/testImages')
        .then(response => {
          console.log(response.data)
          this.testImages = response.data
        })
        .catch(error => { console.log(error) })
    },
    changeImage (id) {
      axios.get('/api/v1/Project/setActiveImage?imgName=' + id)
        .then(response => {
          console.log(response.data)
        })
        .catch(error => { console.log(error) })
    },
    sendCmd () {
      axios.get('/api/v1/Project/sendCmd?CMD=detect')
        .then(response => {
          bus.$emit('measuredValues', response)
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  mounted: function () {
    this.getTestImages()
  },
  beforeDestroy: function () {
  }
}
</script>
<style scoped>
@import  '~vue-context/dist/css/vue-context.css';
.tool {
  font-size: 1.0rem;
}
</style>



