<template>
  <div id="listTargets">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Prfplne</p>
        <button class="delete is-pulled-right" aria-label="close" @click="$router.push('/plugins');closePartsModal()"></button>
      </header>
      <section class="modal-card-body">
        <b-table
          :data='data'
          :bordered='isBordered'
          :striped='isStriped'
          :narrowed='isNarrowed'
          :hoverable='isHoverable'
          :loading='isLoading'
          :focusable='isFocusable'
          :mobile-cards='hasMobileCards'
          :paginated="isPaginated"
          :per-page="perPage"
          :current-page.sync="currentPage"
          :pagination-simple="isPaginationSimple"
          :pagination-position="paginationPosition"
          :default-sort-direction="defaultSortDirection"
          :sort-icon="sortIcon"
          :sort-icon-size="sortIconSize"
          default-sort="user.first_name"
          aria-next-label="Next page"
          aria-previous-label="Previous page"
          aria-page-label="Page"
          aria-current-label="Current page">
          <template slot-scope='props'>
            <!--b-table-column field='plugin'  label='Plugin' width='40' numeric>{{ props.row.id }}</b-table-column>
            <b-table-column field='id'      label='ID'>{{ props.row.id }}</b-table-column-->
            <b-table-column sortable field='edit'    label=''><button class="button is-small modal-button" @click="edit(props.row.number, props.row.version)"><i class="fas fa-edit"></i></button></b-table-column>
            <b-table-column sortable field='updated'    label='updated'>{{ props.row.ts.split('+')[0] }}</b-table-column>
            <b-table-column sortable field='part'      label='part'>{{ props.row.number }}</b-table-column>
            <b-table-column sortable field='version'      label='version' centered>{{ props.row.version }}</b-table-column>
            <b-table-column sortable field='remove'      label='' centered><button class="button is-danger is-small" @click="remove(props.row)"><i class="fas fa-trash"></i></button></b-table-column>
          </template>
          <template slot='empty'>
            <section class='section'>
              <div class='content has-text-grey has-text-centered'>
                <p>
                  <b-icon icon='emoticon-sad' size='is-large'></b-icon>
                </p>
                <p>Nothing here.</p>
              </div>
            </section>
          </template>
        </b-table>
      </section>
    </div>
    <div id="modal-parts" class="modal" v-bind:class="{ 'is-active': isShowModal }">
      <div class="modal-background"></div>
      <div class="modal-card" v-if="editData.coi">
        <header class="modal-card-head">
          <p class="modal-card-title"><txt tid="part"/></p>
          <button class="delete" aria-label="close" @click="isShowModal = false"></button>
        </header>
        <section class="modal-card-body">
          <!-- Content ... -->
          <div>
            <div class="field is-horizontal" v-for="(edt, name) in editData" :key="edt.id" :id="'edit_' + name">
              <div class="field-label" v-if="typeof edt != 'object' && !fields[name].hidden">
                <label class="label"><txt :tid="name"/></label>
              </div>
              <div class="field-body" v-if="typeof edt != 'object' &&  !fields[name].hidden">
                <div class="field">
                  <div class="control" v-if="name != 'status'">
                    <input class="input" v-model="editData[name]">
                  </div>
                  <div class="control" v-if="name == 'status'">
                    <div class="select">
                      <txtSelect tid="order-status-select" v-model="editData[name]"></txtSelect>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="tabs">
            <ul>
              <li v-for="(cidx, index) in editData.coi" :key="index" :id="'tab_' + index" :class="{ 'is-active': selectedCoi == index }" @click="selectedCoi=index">
                <a>M{{index+1}}</a>
              </li>
            </ul>
          </div>
          <div class="tabContent" v-if="editData.coi">
            <div class="field is-horizontal" v-for="(coiEdt, attr) in editData.coi[selectedCoi]" :key="attr" :id="'edit_' + attr">
              <div class="field-label is-normal" v-if="!fields[attr].hidden">
                <label class="label"><txt :tid="attr"/></label>
              </div>
              <div class="field-body" v-if="!fields[attr].hidden">
                <div class="field">
                  <div class="control">
                    <input class="input" v-model="editData.coi[selectedCoi][attr]">
                  </div>
                </div>
              </div>
            </div>  
          </div>
        </section>
        <footer class="modal-card-foot">
          <button class="button is-success" @click="save(editData);isShowModal = false"><i class="fas fa-save"></i></button>
          <button class="button" @click="isShowModal = false"><i class="fas fa-window-close"></i></button>
          <div style="position: absolute; right: 25px;">
            <button class="button is-info" @click="copyCoi()"><i class="fas fa-plus"></i></button>
            <button class="button" :disabled="editData.coi.length < 2" @click="delCoi()"><i class="fas fa-trash"></i></button>
          </div>
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
import bus from '@/components/bus'
import axios from 'axios'
import txt from '@/components/txt'

export default {
  name: 'listParts',
  props: ['activePart', 'targets'],
  components: {
    txt
  },
  data () {
    return {
      data: [],
      index: 0,
      editData: {},
      fields: [],
      selectedPart: {},
      selectedCoi: 0,
      isShowModal: false,
      countPerPage: 10,
      isEmpty: false,
      isBordered: false,
      isStriped: true,
      isNarrowed: true,
      isHoverable: true,
      isFocusable: false,
      isLoading: false,
      hasMobileCards: false,
      isPaginated: true,
      isPaginationSimple: true,
      paginationPosition: 'bottom',
      defaultSortDirection: 'asc',
      sortIcon: 'arrow-up',
      sortIconSize: 'is-small',
      currentPage: 1,
      perPage: 10,
      targetArray: [
        [
          {'type': 'TEST'}
        ]
      ]
    }
  },
  methods: {
    edit (part, version) {
      console.log('in edit ' + part)
      for (var i = 0; i < this.data.length; i++) {
        if (part === this.data[i].number && version === this.data[i].version) {
          this.getPart(this.data[i].number, parseInt(this.data[i].version))
        }
      }
    },
    remove (p) {
      console.log('in remove ' + p.number)
      for (var i = 0; i < this.data.length; i++) {
        if (p.number === this.data[i].number && p.version === this.data[i].version) {
          axios.get('/api/v1/Project/delPart?number=' + p.number + '&version=' + p.version)
            .then(response => {
              console.log(response.data)
              this.getPartsList()
            })
            .catch(error => { console.log(error) })
          break
        }
      }
    },
    newPart (task) {
      console.log('in newPart')
      var number = 'new'
      var version = 1
      if (this.selectedPart !== {}) {
        number = this.selectedPart.number
        version = this.selectedPart.version
        this.selectedPart = {}
      }
      axios.get('/api/v1/Project/getPart?number=' + number + '&version=' + version)
        .then(response => {
          console.log(response.data)
          var copied = response.data
          if (task === 'copy') {
            copied.number = '_' + copied.number
          } else {
            copied.number = task
          }
          copied.version = 1
          var self = this
          this.save(copied, function () {
            self.getPartsList()
          })
        })
        .catch(error => { console.log(error) })
    },
    getPart (name, version, cb) {
      axios.get('/api/v1/Project/getPart?number=' + name + '&version=' + version)
        .then(response => {
          this.editData = response.data
          console.log(this.editData)
          this.isShowModal = true
        })
        .catch(error => { console.log(error) })
    },
    save (data, cb = null) {
      this.isShowModal = false
      var name = data.number
      var version = parseInt(data.version)
      axios.post('/api/v1/Project/savePart?name=' + name + '&version=' + version, data)
        .then(response => {
          console.log(response.data)
          if (cb !== null) {
            cb()
          } else {
            this.getPartsList()
            // If the part is active then update App and have
            // the changes distributed via props
            if (data.number === this.activePart.number) {
              bus.$emit('updatePart', data)
            }
          }
        })
        .catch(error => { console.log(error) })
      bus.$emit('infoMessage', 'Prfplan gespeichert')
    },
    close () {
      console.log('in close')
      this.isShowModal = false
    },
    page (page) {
      this.currentPage = page
      if (this.currentPage < 1) {
        this.currentPage = 1
      }
      if ((this.currentPage - 1) * this.countPerPage > this.total) {
        this.currentPage = parseInt(this.total / this.countPerPage) + 1
      }
      this.index = (this.currentPage - 1) * this.countPerPage
      console.log(this.index)
      this.getPartsList()
    },
    getPartsList () {
      axios.get('/api/v1/Project/listParts?index=' + this.index + '&count=' + this.countPerPage)
        .then(response => {
          this.total = response.data.total
          this.data = response.data.list
          console.log(this.data)
        })
        .catch(error => { console.log(error) })
    },
    selectPart (p) {
      for (var i = 0; i < this.data.length; i++) {
        document.getElementById('partsList_' + this.data[i].number + '_' + this.data[i].version).classList.remove('is-selected')
        if (this.data[i].number === p.number && this.data[i].version === p.version) {
          document.getElementById('partsList_' + this.data[i].number + '_' + this.data[i].version).classList.add('is-selected')
          this.selectedPart = p
        }
      }
    },
    copyCoi () {
      this.editData.coi.push(JSON.parse(JSON.stringify(this.editData.coi[this.selectedCoi])))
      this.selectedCoi = this.editData.coi.length - 1
      for (var i = 0; i < this.editData.coi.length; i++) {
        this.editData.coi[i].id = i
      }
      console.log('copied Coi')
    },
    delCoi () {
      if (this.editData.coi.length > 1) {
        this.editData.coi.splice(this.selectedCoi, 1)
        this.selectedCoi -= 1
        if (this.selectedCoi < 0) {
          this.selectedCoi = 0
        }
        for (var i = 0; i < this.editData.coi.length; i++) {
          this.editData.coi[i].id = i
        }
        console.log('deleted Coi')
      }
    },
    closePartsModal () {
      bus.$emit('togglePartsModal')
    }
  },
  mounted: function () {
    this.fields['number'] = {hidden: false}
    this.fields['name'] = {hidden: false}
    this.fields['version'] = {hidden: false}
    this.fields['stl'] = {hidden: false}
    this.fields['id'] = {hidden: true}
    this.fields['type'] = {hidden: true}
    this.fields['ut'] = {hidden: false}
    this.fields['uw'] = {hidden: false}
    this.fields['nm'] = {hidden: false}
    this.fields['lw'] = {hidden: false}
    this.fields['lt'] = {hidden: false}
    this.fields['units'] = {hidden: false}
    this.fields['formula'] = {hidden: false}
    this.getPartsList()
  }
//  mounted: function () {
//    setTimeout(() => {
//      this.targetArray = this.targets
//    }, 200)
//  }
}
</script>
<style>
.modal-card {
  min-width: 700px;
}
</style>


