<template>
  <div id="listTargets">
    <b-table
      class="measuredValuesTable"
      :data='targetArray'
      :bordered='isBordered'
      :striped='isStriped'
      :narrowed='isNarrowed'
      :hoverable='isHoverable'
      :loading='isLoading'
      :focusable='isFocusable'
      :mobile-cards='hasMobileCards'
      :paginated="isPaginated"
      :per-page="perPage"
      :current-page.sync="currentPage"
      :pagination-simple="isPaginationSimple"
      :pagination-position="paginationPosition"
      :default-sort-direction="defaultSortDirection"
      :sort-icon="sortIcon"
      :sort-icon-size="sortIconSize"
      :row-class="(row, index) => allocateColor(row)"
      aria-next-label="Next page"
      aria-previous-label="Previous page"
      aria-page-label="Page"
      aria-current-label="Current page">
      <template slot-scope='props'>
        <!--b-table-column field='plugin'  label='Plugin' width='40' numeric>{{ props.row.id }}</b-table-column>
        <b-table-column field='id'      label='ID'>{{ props.row.id }}</b-table-column-->
        <b-table-column sortable field='name'      label='Name'>{{ props.row.name }}</b-table-column>
        <b-table-column sortable numeric field='value'     label='Messwert'>{{ parseFloat(props.row.value).toFixed(3) }}</b-table-column>
        <b-table-column sortable numeric field='ut'        label='OT'>{{ parseFloat(props.row.ut).toFixed(3) }}</b-table-column>
        <b-table-column sortable numeric field='nm'        label='NM'>{{ parseFloat(props.row.nm).toFixed(3) }}</b-table-column>
        <b-table-column sortable numeric field='lt'        label='UT'>{{ parseFloat(props.row.lt).toFixed(3) }}</b-table-column>
      </template>
      <template slot='empty'>
        <section class='section'>
          <div class='content has-text-grey has-text-centered'>
            <p>
              <b-icon icon='emoticon-sad' size='is-large'></b-icon>
            </p>
            <p>Nothing here.</p>
          </div>
        </section>
      </template>
    </b-table>
  </div>
</template>

<script>
import bus from '@/components/bus'
import axios from 'axios'
export default {
  name: 'sidebarMeasuredValues',
  props: ['targets'],
  components: {
  },
  data () {
    return {
      isEmpty: false,
      isBordered: false,
      isStriped: false,
      isNarrowed: true,
      isHoverable: false,
      isFocusable: false,
      isLoading: false,
      hasMobileCards: false,
      isPaginated: false,
      isPaginationSimple: false,
      paginationPosition: 'bottom',
      defaultSortDirection: 'asc',
      sortIcon: 'arrow-up',
      sortIconSize: 'is-small',
      currentPage: 1,
      perPage: 10,
      measuredValues: {},
      extraValues: {},
      targetArray: [
        {'id': 'blablal', 'name': 'name', 'value': 3.3, 'status': 1},
        {'id': 'blablal1', 'name': 'name', 'value': 3.3, 'status': 1},
        {'id': 'blablal2', 'name': 'name', 'value': 3.3, 'status': 0},
        {'id': 'blablal3', 'name': 'name', 'value': 3.3, 'status': 1},
        {'id': 'blablal4', 'name': 'name', 'value': 3.3, 'status': 1}
      ]
    }
  },
  methods: {
    allocateColor (row) {
      if (row.status === 0) {
        return 'greenRow'
      } else {
        return 'redRow'
      }
    },
    getRowClass (row, index) {
      console.log(row)
      console.log(index)
    },
    closeTargetsModal () {
      bus.$emit('toggleTargets')
    },
    fillTargetArray (values) {
      this.measuredValues = values.data.record.values

      this.targetArray = []
      var targetObject = {}
      var dimensions = values.data.plan.coi

      for (var i = 0; i < dimensions.length; i++) {
        targetObject = { 'id': i, 'name': dimensions[i].name, 'value': 'No value', 'status': 0, 'ut': 0, 'uw': 0, 'nm': 0, 'lw': 0, 'lt': 0 }
        this.targetArray.push(targetObject)
      }
      console.log(dimensions)
      for (i = 0; i < this.measuredValues.length; i++) {
        if (i < this.targetArray.length) {
          this.targetArray[i].value = this.measuredValues[i].v
          this.targetArray[i].status = this.measuredValues[i].s
          this.targetArray[i].ut = dimensions[i].ut
          this.targetArray[i].uw = dimensions[i].uw
          this.targetArray[i].nm = dimensions[i].nm
          this.targetArray[i].lw = dimensions[i].lw
          this.targetArray[i].lt = dimensions[i].lt
        }
      }
      // this.targetArray[0].id = this.measuredValues.data.plan
      // this.targetArray[1] = this.measuredValues.data.record.values
      // console.log(this.targetArray)
    }
  },
  mounted: function () {
    axios.get('/api/v1/Project/getLastParams')
      .then(response => {
        this.fillTargetArray(response)
      })
      .catch(error => { console.log(error) })
    bus.$on('measuredValues', values => {
      console.log(values)
      this.fillTargetArray(values)
    })
  }
}
</script>
<style>
.modal-card {
  min-width: 700px;
}
tr.redRow {
  background: #f01616;
  color: whitesmoke;
}
tr.greenRow {
  background: green;
  color: whitesmoke;
}
.measuredValuesTable table th, .measuredValuesTable table td {
  overflow-x: hidden;
  font-size: 0.9rem;
}
table th, table td {
  white-space: nowrap;
}
</style>


