<template>
  <div class="containerProgressBar">
    <table class="table is-narrow progressbar" v-if="activeProject">
      <tr class="listItem"  v-for="(plugins, index) in activeProject.plugins" :key="index" :id="'listElement_' + plugins.name">
          <td><router-link to="/plugins"><button :id="plugins.name + 'button'" :class="{ isActive: activePlugin == plugins.name }" @click="activatePlugin(plugins.name)">{{plugins.name}}</button></router-link></td>
          <td class="milliseconds">{{cycleTimes[index]}} ms</td>
      </tr>
      <tr><td>Total</td><td class="milliseconds">{{totalCycleTime}} ms</td></tr>
      <tr><td>{{activePlugin}}</td></tr>
    </table>
  </div>
</template>
<script>
/* eslint-disable */
import bus from '@/components/bus'
import axios from 'axios'

export default {
  name: 'progressBar',
  props: ['activeProject', 'activePlugin', 'pluginConfig'],
  data () {
    return {
      maxPlugins: 20,
      startTimes: [],
      cycleTimes: [],
      totalCycleTime: 0
    }
  },
  methods: {
    activatePlugin (name) {
      activePlugin = name
      axios.get('/api/v1/' + activePlugin + '/getConfig')
        .then(response => {
          // console.log(response.data)
          pluginConfig = response.data
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  mounted: function () {
    for (var i=0; i < this.maxPlugins; i++) {
      this.startTimes[i] = 0
      this.cycleTimes[i] = 0
    }
    var self = this
    bus.$on('pluginStatusUpdate', function (pin, status) {
      console.log(pin, status)
      var e = document.getElementById('listElement_' + pin)
      if (status == '$Start') {
        for (var i = 0; i < self.activeProject.plugins.length; i++) {
          console.log(self.activeProject.plugins[i].name, pin)
          if (self.activeProject.plugins[i].name == pin) {
            self.startTimes[i] = Date.now()
            break
          }
        }
        e.classList.add('active')
      }
      if (status == '$Finished') {
        self.totalCycleTime = 0
        for (var i = 0; i < self.activeProject.plugins.length; i++) {
          if (self.activeProject.plugins[i].name == pin) {
            self.$set(self.cycleTimes, i,  Date.now() - self.startTimes[i])
          }
          self.totalCycleTime += self.cycleTimes[i]
        }
        setTimeout(function() {
          e.classList.remove('active')
        }, 250)
      }
    })
  }
}
</script>
<style scoped>
@font-face {
  font-family: CenturyGothic;
  src: url('../../assets/fonts/CenturyGothic.ttf');
}
.listItem {
  background-color: aliceblue;
  font-size: 1rem;
}
.listItem.active {
  background-color: lightgreen;
}
.milliseconds {
  text-align: right;
  white-space: nowrap;
}
</style>


