<template>
  <div class="" id="navBarAbovePluginViewer" :class="{ pluginBackground: $route.path == '/plugins'}">
    <nav class="navbar" role="navigation">
      <div id="navbarVision" class="navbar-menu">
        <div class="navbar-start" v-if="activeProject != {}">
          <div class="navbar-item">
            <div class="dropdown is-hoverable">
              <div class="dropdown-trigger">
                <button class="button sideBarTopDropdown" aria-haspopup="true" aria-controls="ansicht">
                  <span>Projekt</span>
                  <span class="icon is-small">
                  <i class="fas fa-angle-down" aria-hidden="true"></i>
                  </span>
                </button>
              </div>
              <div class="dropdown-menu" id="ansicht" role="menu">
                <div class="dropdown-content">
                  <div class="dropdown-item" v-if="runningMode == 1" @click="setRunningMode(0)">
                  Config Modus
                  </div>
                  <div class="dropdown-item" v-if="runningMode == 0" @click="setRunningMode(1)">
                  Auto Modus
                  </div>
                </div>
              </div>
            </div>
          </div>  
          <div class="navbar-item">
            <div class="dropdown is-hoverable">
              <div class="dropdown-trigger">
                <button class="button sideBarTopDropdown" aria-haspopup="true" aria-controls="ansicht">
                  <span>Ansicht</span>
                  <span class="icon is-small">
                    <i class="fas fa-angle-down" aria-hidden="true"></i>
                  </span>
                </button>
              </div>
              <div class="dropdown-menu" id="ansicht" role="menu">
                <div class="dropdown-content">
                  <div v-if="!showParts" class="dropdown-item" @click="$router.push('/parts');toggleParts()">
                      Teile <span class="icon is-pulled-right" v-if="showParts"><i class="fas fa-check"></i></span>
                  </div>
                  <div v-if="showParts" class="dropdown-item" @click="$router.push('/plugins');toggleParts()">
                      Teile <span class="icon is-pulled-right" v-if="showParts"><i class="fas fa-check"></i></span>
                  </div>
                  <div v-if="!showOrders" class="dropdown-item" @click="$router.push('/orders');toggleOrders()">
                      Prfauftrge <span class="icon is-pulled-right" v-if="showOrders"><i class="fas fa-check"></i></span>
                  </div>
                  <div v-if="showOrders" class="dropdown-item" @click="$router.push('/plugins');toggleOrders()">
                      Prfauftrge <span class="icon is-pulled-right" v-if="showOrders"><i class="fas fa-check"></i></span>
                  </div>
                  <div v-if="!showTargets" class="dropdown-item" @click="$router.push('/targets');toggleTargets()">
                      Objektliste<span class="icon is-pulled-right" v-if="showTargets"><i class="fas fa-check"></i></span>
                  </div>
                  <div v-if="showTargets" class="dropdown-item" @click="$router.push('/plugins');toggleTargets()">
                      Objektliste<span class="icon is-pulled-right" v-if="showTargets"><i class="fas fa-check"></i></span>
                  </div>
                  <hr class="dropdown-divider">
                  <div class="dropdown-item" @click="changeMainView(1)">
                      Statistiken <span class="icon is-pulled-right" v-if="showCharts"><i class="fas fa-check"></i></span>
                  </div>
                  <div class="dropdown-item" @click="changeMainView(0)">
                      Kanal 1 <span class="icon is-pulled-right" v-if="!showCharts"><i class="fas fa-check"></i></span>
                  </div>
                  <div class="dropdown-item disabledItem" disabled>
                      Kanal 2
                  </div>
                </div>
              </div>
            </div>  
          </div>
          <div class="navbar-item">
            <button class="button sideBarTopDropdown" @click="resetImage()">Bild zurcksetzen</button>
          </div>
        </div>
        <div class="navbar-end">
          <div class="navbar-item">
            <span v-if="runningMode == 1">Auto Modus</span>
          </div>
          <div class="navbar-item">
            <span v-if="runningMode == 0">Config Modus</span>
          </div>
          <div class="navbar-item">
            <button class="button sideBarTopDropdown" @click="loginModal = true">Login</button>
          </div>
        </div>
      </div>
    </nav>
    <div id="modal-parts" class="modal" v-bind:class="{ 'is-active': loginModal }">
    <div class="modal-background" @click="loginModal = false"></div>
      <div class="modal-card">
        <section class="modal-card-head">
        Login
        </section>
        <section class="modal-card-body">
          <div class="field">
              <p class="control has-icons-left has-icons-right">
              <input class="input" type="text" placeholder="Benutzer">
              <span class="icon is-small is-left">
                  <i class="fas fa-user"></i>
              </span>
              <span class="icon is-small is-right">
                  <i class="fas fa-check"></i>
              </span>
              </p>
          </div>
          <div class="field">
              <p class="control has-icons-left">
              <input class="input" type="password" placeholder="Password">
              <span class="icon is-small is-left">
                  <i class="fas fa-lock"></i>
              </span>
              </p>
          </div>
        </section>
        <footer class="modal-card-foot">
          <button class="button is-success" @click="setRunningMode(0);loginModal = false"><i class="fas fa-save"></i></button>
          <button class="button" @click="loginModal = false"><i class="fas fa-window-close"></i></button>
        </footer>
      </div>
    </div>  
  </div>
</template>
<script>
/* eslint-disable */
import bus from '@/components/bus'
import txt from '@/components/txt'
import axios from 'axios'

export default {
  name: 'navbarTop',
  props: ['activeProject', 'runningMode', 'showParts', 'showTargets', 'showOrders', 'showCharts'],
  components: {
      txt
  },
  data () {
    return {
      loginModal: false
    }
  },
  methods: {
    changeMainView (target) {
      var self = this
      if (target == 1) {
        this.toggleCharts(1)
      }
      if (target == 0) {
        this.toggleCharts(0)
        setTimeout(() => {
          this.toggleFrameIn()        
        }, 100)
      }
    },
    setRunningMode (mode) {
      axios.get('api/v1/Project/setRunningMode?runningMode=' + mode)
        .then(response => {
          bus.$emit('setRunningMode', mode)
          // this.runningMode = mode
        })
      bus.$emit('reloadImage')
    },
    toggleFrameIn () {
      bus.$emit('showFrameIn')
    },
    toggleParts () {
      bus.$emit('togglePartsModal')
    },
    toggleOrders () {
      bus.$emit('toggleOrdersModal')
    },
    toggleCharts (n) {
      bus.$emit('showCharts', n)
    },
    toggleTargets () {
      bus.$emit('toggleTargets')
    },
    resetImage () {
      bus.$emit('resetImage')
    }
  },
  mounted: function () {
  }
}
</script>
<style scoped>
@font-face {
  font-family: CenturyGothic;
  src: url('../../assets/fonts/CenturyGothic.ttf');
}
</style>


