import Vue from 'vue'
import Router from 'vue-router'

import logs from '@/components/logViewer'
import roboCTL from '@/components/roboCTL'

// import pluginViewer from '@/components/aiCore/pluginViewer'
import listParts from '@/components/aiCore/listParts'
// import listParts2 from '@/components/aiCore/listParts2'
import listOrders from '@/components/aiCore/listOrders'
// import listOrders2 from '@/components/aiCore/listOrders2'
import listTargets from '@/components/aiCore/listTargets'
import configuration from '@/components/aiCore/systemConfiguration'

// import modeEdit from '@/components/modeEdit'
// import modeRuntime from '@/components/modeRuntime'

Vue.use(Router)
export default new Router({
  routes: [
    // { props: true, path: '/', component: pluginViewer },
    { props: true, path: '/parts', component: listParts },
    // { props: true, path: '/parts2', component: listParts2 },
    { props: true, path: '/orders', component: listOrders },
    // { props: true, path: '/orders2', component: listOrders2 },
    { props: true, path: '/targets', component: listTargets },
    { props: true, path: '/configuration', component: configuration },
    { props: true, path: '/logs', component: logs },
    { props: true, path: '/roboCTL', component: roboCTL }
    // { props: true, path: '/plugins', component: pluginViewer }
    // { props: true, path: '/modeedit', component: modeEdit },
    // { props: true, path: '/moderuntime', component: modeRuntime }
  ]
  // mode: 'history'
})



