
<template>
  <div>
    <div id="tables">
      <div id="axisValues">
        <table v-if="status" class="table is-fullwidth is-narrow">
          <tr>
            <th>Axis</th>
            <th>Position</th>
          </tr>
          <tr>
            <td>X</td>
            <td>{{status.x.toFixed(3)}}</td>
          </tr>
          <tr>
            <td>Y</td>
            <td>{{status.y.toFixed(3)}}</td>
          </tr>
          <tr>
            <td>Z</td>
            <td>{{status.z.toFixed(3)}}</td>
          </tr>
          <tr>
            <td>A</td>
            <td>{{status.a.toFixed(3)}}</td>
          </tr>
          <tr>
            <td>B</td>
            <td>{{status.b.toFixed(3)}}</td>
          </tr>
          <tr>
            <td>C</td>
            <td>{{status.c.toFixed(3)}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'roboCTLPositionTable',
  components: {
  },
  props: ['status'],
  data () {
    return {
    }
  },
  methods: {
  },
  mounted () {
  }
}
</script>
<style scoped>
table th:nth-child(2) { text-align: right; }
table th:nth-child(3) { text-align: right; }
table td:nth-child(2) { text-align: right; }
table td:nth-child(3) { text-align: right; }


</style>


