<template>
  <div>
    <div id="projectForm" class="columns">
      <div class="column is-3">
        <div class="field">
          <label class="label">Project</label>
          <div class="control">
            <div class="select">
              <select id="selectProject" @change="onChange($event)">
                <option>select Project</option>
                <option value="newProject">create new Project</option>
                <option v-for="project in projects" :key="project" :value="project" :selectedProject="project">
                  {{project}}
                </option>
              </select>
            </div>
          </div>
        </div>
        <div class="field" v-if="selectedProject != ''">
          <label class="label">Select Plugin</label>
          <div class="control">
            <div class="select">
              <select>
                <option v-for="plugin in plugins" :key="plugin" :value="plugin">
                  {{plugin}}
                </option>
              </select>
            </div>
          </div>
        </div>
      </div>
      <div class="column is-9" v-if="cdata && selectedProject != ''">
        <div class="field is-horizontal" v-if="cdata && selectedProject != ''">
          <div class="field-label is-normal">
            <label class="label">Project name</label>
          </div>
          <div class="field-body">
            <div class="field">
              <p class="control">
                <input class="input" type="text" v-model="cdata.projectName" readonly>
              </p>
            </div>
          </div>
        </div>
        <div class="field is-horizontal" v-if="cdata && selectedProject != ''">
          <div class="field-label is-normal">
            <label class="label">vistaServer</label>
          </div>
          <div class="field-body">
            <div class="field">
              <p class="control">
                <input class="input" type="text" v-model="cdata.vistaServer" readonly>
              </p>
            </div>
          </div>
        </div>
        <div class="field is-horizontal" v-if="cdata && selectedProject != ''">
          <div class="field-label is-normal">
            <label class="label">Node Red</label>
          </div>
          <div class="field-body">
            <div class="field">
              <p class="control">
                <input class="checkbox" type="checkbox" :checked="cdata.nodeRed" readonly>
              </p>
            </div>
          </div>
        </div>
        <div class="field is-horizontal" v-if="cdata && selectedProject != ''">
          <div class="field-label is-normal">
            <label class="label">Plugins</label>
          </div>
          <div class="field-body">
            <div class="field">
              <p class="control">
                <table>
                  <tr><th>Id</th><th>Plugin</th></tr>
                  <tr v-for="(item, idx) in cdata.plugins" :key="idx">
                    <td class="pluginId"><input class="input" type="number" :value="idx" readonly></td>
                    <td class="pluginSelect">
                      <div class="control">
                        <div class="select">
                          <select :id="'selectPlugin_' + idx" @change="onChange($event)">
                            <option v-for="plugin in plugins" :key="plugin" :value="plugin" :selected="'plugin' + item.name == plugin">
                              {{plugin}}
                            </option>
                          </select>
                        </div>
                      </div>
                    </td>
                    <!--td class="pluginCBox" v-for="prog in cdata.programs" :key="prog.name">
                      <input :id="'cBox_' + prog + '_' + idx" type="checkbox" @change="onChange($event)" :checked="checkIfChecked(item, prog)">
                    </td-->
                    <!--td class="pluginCBox"><button class="button is-danger is-small" @click="removePlugin(id)">Delete</button></td-->
                  </tr>
                </table>
              </p>
            </div>
          </div>
        </div>
        <!--button class="button is-primary" @click="save()">Save</button>
        <button class="button is-primary">Update</button-->
      </div>
    </div>
    <hr>
    {{activeProject}}
  </div>
</template>
<script>
import axios from 'axios'

export default {
  name: 'configuration',
  props: ['pluginConfig', 'activeProject'],
  components: {
  },
  data () {
    return {
      cdata: {},
      projects: [],
      selectedProject: '',
      plugins: []
    }
  },
  watch: {
  },
  methods: {
    checkIfChecked (plugin, program) {
      // console.log(plugin, program)
      for (var i = 0; i < this.cdata.programs.length; i++) {
        if (this.cdata.programs[i].name === program.name) {
          for (var j = 0; j < this.cdata.programs[i].sequence.length; j++) {
            if (this.cdata.programs[i].sequence[j] === plugin.id) {
              // console.log(this.cdata.programs[i].sequence[j])
              return (true)
            }
          }
        }
      }
      return (false)
    },
    removePlugin (what) {
      this.removeIDFromArray('plugins', what, this.cdata.plugins)
      for (var i = 0; i < this.cdata.programs.length; i++) {
        this.removeIDFromArray('programs', what, this.cdata.programs[i].sequence)
      }
    },
    removeIDFromArray (name, what, arr) {
      var i
      var deleted = false
      for (i = 0; i < arr.length; i++) {
        if (name === 'plugins') {
          if (arr[i].id === what) {
            arr.splice(i, 1)
            deleted = true
          }
        }
        if (name === 'programs') {
          if (arr[i] === what) {
            arr.splice(i, 1)
            deleted = true
          }
        }
      }
      for (i = 0; i < arr.length; i++) {
        if (i >= what) {
          if (name === 'plugins' && deleted) {
            arr[i].id = i
          }
          if (name === 'programs' && deleted) {
            arr[i] = i
          }
        }
      }
    },
    onChange (event) {
      console.log(event.target.id)
      console.log(event.target.value)
      var id
      if (event.target.id === 'selectProject') {
        this.selectedProject = event.target.value
        this.updatePluginList()
      }
      if (event.target.id.indexOf('selectPlugin_') === 0) {
        id = event.target.id.split('_')[1]
        this.cdata.plugins[id].name = event.target.value
      }
      if (event.target.id.indexOf('cBox_auto_') === 0) {
        id = event.target.id.split('_')[2]
        this.cdata.autostartplugins = []
        this.cdata.autostartplugins = this.cdata.autostartplugins.push(this.cdata.plugins[id].name)
      }
    },
    updatePluginList () {
      axios.get('/api/v1/Project/listPlugins')
        .then(response => {
          this.plugins = response.data
          axios.get('/api/v1/Project/listPlugins?project=' + this.selectedProject)
            .then(response => {
              console.log(response.data)
              for (var p = 0; p < response.data.length; p++) {
                this.plugins.push(response.data[p])
              }
              axios.get('/api/v1/Project/getProject?project=' + this.selectedProject)
                .then(response => {
                  console.log(response.data)
                  this.cdata = response.data
                })
                .catch(error => {
                  console.log(error)
                })
            })
            .catch(error => {
              console.log(error)
            })
        })
        .catch(error => {
          console.log(error)
        })
    },
    save () {
      console.log(this.cdata)
      axios.post('/api/v1/Project/saveProject?project=' + this.selectedProject, this.cdata)
        .then(response => {
          console.log(response.data)
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  mounted () {
    axios.get('/api/v1/Project/listProjects')
      .then(response => {
        this.projects = response.data
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>
<style scoped>
#projectForm {
  text-align: left;
}
.pluginId {
  width:50px;
}
.pluginCBox {
  width:100px;
  text-align:center;
}
</style>


