<template>
  <div>
    <div id="notificationDivWrapper" v-if="notification != ''">
      <div class="notification is-danger">
        <button class="delete" @click="notification = ''"></button>
        <span id="notificationDiv">{{notification}}</span>
      </div>
    </div>
    <div id="informationDivWrapper" v-show="information != ''">
      <div class="notification is-warning">
        <button class="delete" @click="information = ''"></button>
        <span id="informationDiv">{{information}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import bus from '@/components/bus'

export default {
  name: 'notificationDisplay',
  props: [],
  components: {
  },
  data () {
    return {
      notification: '',
      information: ''
    }
  },
  methods: {
  },
  mounted: function () {
    console.log('in notificationDisplay mounted')
    bus.$on('showInformation', info => {
      this.information = info
    })
    bus.$on('showNotification', info => {
      this.notification = info
    })
    bus.$on('errorMessage', error => {
      this.notification = error
    })
    bus.$on('infoMessage', info => {
      this.information = info.replace('$Info - ', '')
      console.log(this.information)
      setTimeout(() => { this.information = '' }, 2000)
    })
  },
  beforeDestroy: function () {
    console.log('in notificationDisplay beforeDestroy')
    bus.$off('showInformation', null)
    bus.$off('showNotification', null)
    bus.$off('infoMessage', null)
  }
}
</script>
<style scoped>
@font-face {
  font-family: CenturyGothic;
  src: url('../../assets/fonts/CenturyGothic.ttf');
}
</style>


