<template>
  <div id="toolbox">
    <div>
      <div id="toolListContainer" class="columns is-centered is-marginless">
        <span class="toollist" v-for="(tool,idx) in tools" :key="idx">
          <div class="dropdown is-hoverable">
            <div class="dropdown-trigger">
              <button :class="'button menuListBtn ' + selectedTool(tool)"
                :disabled="tool.disabled"
                :data-tooltip="tool.tooltip"
                @click="selectTool(tool)"
                aria-haspopup="true" 
                aria-controls="dropdown-menu4">
                <i :class="tool.icon"></i>
              </button>
            </div>
            <div class="dropdown-menu" id="dropdown-menu4" role="menu">
              <div class="dropdown-content">
                <div class="dropdown-item">
                  {{tool.name}}
                </div>
              </div>
            </div>
          </div>
        </span>
      </div>
    </div>
    <div class="" style="margin-top:50px;">
      <div class="" v-if="activeTool == 'menu'">
        <table class="table is-striped is-hoverable is-fullwidth is-narrow">
          <tr v-for="(tool,idx) in tools" :key="idx" @click="selectTool(tool)">
            <td class="menutd">
              <button :class="'menuListBtn button is-small ' + selectedTool(tool)"
                :disabled="tool.disabled"
                :data-tooltip="tool.tooltip">
                <i :class="tool.icon"></i>
              </button>
            </td>
            <td class="menutd">
              <txt :tid="tool.tooltip"/>
            </td>
          </tr>
        </table>
      </div>
      <div class="" v-if="activeTool == 'test' && activeProject.statusImg">
        <sidebarMeasuredValues :targets="targets"></sidebarMeasuredValues>
        <br>
        <table class="table is-striped is-hoverable is-fullwidth is-narrow" v-if="params.record">
          <tr><td><txt tid="Time"/></td><td>{{params.record.date.split('T')[1]}}</td></tr>
          <tr><td><txt tid="Part"/></td><td>{{params.record.part}}</td></tr>
          <tr><td><txt tid="Order"/></td><td>{{params.record.order}}</td></tr>
          <tr><td><txt tid="Machine"/></td><td>{{params.record.machine}}</td></tr>
          <!--tr>
            <td><txt tid="inspectionCharacters"/></td>
            <td id="ergebnisBtnList" style="padding: 8px">
              <span v-if="params.record.values.length === 0">
                Kein Prfmerkmal zugewiesen
              </span>
              <span v-if="params.record.values.length > 0">
                <button class="button ergebnislist" v-for="(moi, idx) in params.record.values" :key="idx" :class="statusList[moi.s]">
                  M{{idx+1}} {{(parseFloat(moi.v)).toFixed(3)}}
                </button>
              </span>
            </td>
          </tr-->
        </table>
        <span v-if="activeProject.statusType == 'gif'">
          <img width="200" height="200" :src="'/static/gif/' +  activeProject.statusImg[gifStatus]">
        </span>
        <span v-if="activeProject.statusType == 'svg'">
          <svg height="70" width="70">
            <circle cx="35" cy="35" r="30" stroke="steelblue" stroke-width="3" :fill="[gifStatus ? 'red' : 'darkslategrey']" />
          </svg>
        </span>
      </div>
      <colorPicker v-if="activeTool == 'color'"
        :activePlugin="activePlugin" :pluginConfig="pluginConfig">
      </colorPicker>
      <crop2Reference v-if="activeTool == 'crop2reference'"
        :activePlugin="activePlugin" :pluginConfig="pluginConfig">
      </crop2Reference>
      <rectPicker v-if="activeTool == 'crop'"
        :activePlugin="activePlugin" :pluginConfig="pluginConfig">
      </rectPicker>
      <roiPicker v-if="activeTool == 'roi'"
        :activePlugin="activePlugin" :pluginConfig="pluginConfig" :activePart="activePart" :targets="targets" >
      </roiPicker>
      <!--button @click="sendCmd(cmd)"><i class="fas fa-bolt"></i></button-->
      <imgPicker v-if="activeTool == 'image'"
        :activePlugin="activePlugin" :activeProject="activeProject" :pluginConfig="pluginConfig">
      </imgPicker>
      <measure v-if="activeTool == 'measure'"
        :activePlugin="activePlugin" :activeProject="activeProject" :pluginConfig="pluginConfig" :targets="targets" :activePart="activePart">
      </measure>
      <!--lines v-if="activeTool == 'lines'"
        :activePlugin="activePlugin" :activeProject="activeProject" :pluginConfig="pluginConfig" :targets="targets" :activePart="activePart">
      </lines-->
    </div>
    <!--div id="toolboxDiv">
    </div-->
  </div>
</template>
<script>
import axios from 'axios'
import bus from '@/components/bus'
import colorPicker from '@/components/aiCore/toolColorPicker'
import crop2Reference from '@/components/aiCore/toolCrop2Reference'
import rectPicker from '@/components/aiCore/toolRectPicker'
import roiPicker from '@/components/aiCore/toolRoiPicker'
import testButtons from '@/components/aiCore/toolTestButtons'
import imgPicker from '@/components/aiCore/toolImgPicker'
import measure from '@/components/aiCore/toolMeasure'
import sidebarMeasuredValues from '@/components/aiCore/sidebarMeasuredValues'
import lines from '@/components/aiCore/toolLines'
import txt from '@/components/txt'

export default {
  name: 'toolbox',
  components: {
    colorPicker,
    rectPicker,
    crop2Reference,
    roiPicker,
    testButtons,
    imgPicker,
    measure,
    lines,
    sidebarMeasuredValues,
    txt
  },
  props: ['activePlugin', 'pluginConfig', 'activeProject', 'activePart', 'targets'],
  data () {
    return {
      activeTool: 'test',
      statusList: ['', 'is-danger'],
      tools: [
        // {name: 'menu', disabled: false, tooltip: 'ToolMenu', icon: 'fas fa-question', selected: false},
        {name: 'image', disabled: false, tooltip: 'Selectimage', icon: 'fas fa-image', selected: false},
        {name: 'refresh', disabled: false, tooltip: 'Reloadimage', icon: 'fas fa-redo', selected: false},
        {name: 'crop', disabled: false, tooltip: 'Cropimage', icon: 'fas fa-crop-alt', selected: false},
        {name: 'crop2reference', disabled: false, tooltip: 'ReferenceImage', icon: 'fas fa-layer-group', selected: false},
        {name: 'color', disabled: false, tooltip: 'Colorpicker', icon: 'fas fa-eye-dropper', selected: false},
        {name: 'roi', disabled: false, tooltip: 'Editregionofinterest(ROI)', icon: 'fas fa-list', selected: false},
        // {name: 'circles', disabled: false, tooltip: 'Find circles', icon: 'far fa-circle', selected: false},
        // {name: 'lines', disabled: false, tooltip: 'Find lines', icon: 'fas fa-minus', selected: false},
        {name: 'measure', disabled: false, tooltip: 'Editmeasurements', icon: 'fas fa-ruler', selected: false},
        // {name: 'parts', disabled: false, tooltip: 'Listinspectionplans', icon: 'fas fa-list-ol', selected: false},
        // {name: 'orders', disabled: false, tooltip: 'Listinspectionorders', icon: 'fas fa-list', selected: false},
        {name: 'test', disabled: false, tooltip: 'Startdetectionsequence', icon: 'fas fa-bolt', selected: false}
      ],
      toolRequired: {
        'Default': ['color', 'measure', 'test', 'plan', 'orders'],
        'Mask': ['crop'],
        'Noise': ['roi'],
        'TemplateMatch': ['roi']
      },
      gifStatus: 0,
      params: {}
    }
  },
  watch: {
    activePlugin: function (p) {
      if (this.toolRequired[this.activePlugin] !== undefined) {
        for (var i = 0; i < this.tools.length; i++) {
          this.tools[i].disabled = false
          /* this.tools[i].disabled = true
          if (this.toolRequired[this.activePlugin].indexOf(this.tools[i].name) >= 0) {
            this.tools[i].disabled = false
          } else {
            if (this.toolRequired['Default'].indexOf(this.tools[i].name) >= 0) {
              this.tools[i].disabled = false
            }
          } */
        }
      }
    }
  },
  methods: {
    selectTool (tool) {
      for (var i = 0; i < this.tools.length; i++) {
        if (tool.name === this.tools[i].name) {
          this.tools[i].selected = true
          this.activeTool = this.tools[i].name
        } else {
          this.tools[i].selected = false
        }
      }
      if (this.activeTool === 'test') {
        this.sendCmd('CMD=detect')
      }
      if (this.activeTool === 'refresh') {
        document.location.reload()
      }
      if (this.activeTool === 'parts') {
        this.$router.push('/parts')
      }
      if (this.activeTool === 'orders') {
        this.$router.push('/orders')
      }
    },
    selectedTool (tool) {
      if (tool.name === this.activeTool) {
        return ('is-warning')
      } else {
        return ('is-link')
      }
    },
    sendCmd (cmd) {
      axios.get('/api/v1/Project/sendCmd?' + cmd)
        .then(response => {
          bus.$emit('measuredValues', response)
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    updateToolboxParams (myparams) {
      // console.log(status)
      this.params = myparams
      this.$set(this, 'gifStatus', myparams.status)
      this.$set(this.params, 'record', myparams.record)
    }
  },
  mounted: function () {
    this.tools[0].selected = true
    bus.$on('statusUpdate', this.updateToolboxParams)
  },
  beforeDestroy: function () {
    bus.$off('statusUpdate', this.updateToolboxParams)
  }
}
</script>
<style scoped>

#ergebnisBtnList {
  display: table;
  width: 100%;
  table-layout: fixed;
}
#ergebnisBtnList span {
  display: table-cell;
  text-align: center;
  padding: 8px;
}
#toolListContainer span {
  display: table-cell;
  text-align: center;
}
.toollist {
  padding:2px;
}
.gTooltip {
  position: relative;
  padding: 5px 12px;
}
.gTooltip::before,.gTooltip::after {
  position: absolute;
  left: 50%;
  opacity: 0;
  transition: all ease 0.3s;
}
.gTooltip::before {
  content: '';
  border-width: 10px 8px 0 8px;
  border-style: solid;
  border-color: rgba(0,0,0,0.9) transparent transparent transparent;
  margin-left: -8px;
  top: -10px;
}
.gTooltip::after {
  content: attr(data-tooltip);
  background: rgba(0,0,0,0.9);
  top: -10px;
  font-size: 14px;
  margin-left: -50%;
  padding: 14px;
  border-radius: 10px;
  color: #eee;
  transform: translateY(-100%);
}
.gTooltip:hover::before, .gTooltip:hover::after {
  opacity: 1;
}
.menuList > tr > td:first-of-type {
  width: 20%;
  text-align: center;
}
.menuListBtn {
  width: 35px;
}
.table.is-narrow td {
  padding-top: 1px;
  padding-bottom: 1px;
  font-size: 0.85em;
}
.ergebnislist {
  margin-left:3px;
}
.dropdown-content {
  z-index: 999999 !important;
  width: 130px;
  background: rgba(104, 104, 104, 0.1);
  border: none;
  box-shadow: none;
  text-align: left;
  color:white;
}

</style>


