<template>
  <span>
    <span v-if="!editMode">
      {{getText()}}
      <!--button class="button is-small is-warning" @click="toggleEditMode()">#</button-->
    </span>
    <span v-if="editMode">
      <table>
      <tr>
        <td><input :size="tmpText[0].length" v-model="tmpText[0]"></td>
        <td><!--button class="button is-small is-warning" @click="toggleEditMode()">#</button--></td>
      </tr>
      <tr>
        <td><input :size="tmpText[0].length" v-model="tmpText[1]"></td>
        <td><button class="button is-small is-warning" @click=saveTexts()>!</button></td>
      </tr>
      </table>
    </span>
  </span>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
// import bus from '@/components/bus'

export default {
  name: 'txt',
  props: ['tid', 'typ', 'idx'],
  data () {
    return {
      editMode: false,
      tmpText: ['', '']
    }
  },
  methods: {
    getText () {
      var text = this.tid
      var typ = this.typ
      var idx = this.idx
      if (typ === undefined || typ === '') {
        typ = 'txt'
      }
      if (idx === undefined || idx === '') {
        idx = -1
      } else {
        idx = parseInt(idx)
      }
      var tmp = ''
      // console.log(this.$texts)
      if (typ in this.$texts) {
        // console.log(this.$languageIdx, text, typ, idx)
        if (text in this.$texts[typ]) {
          if (this.$texts[typ][text].length > this.$languageIdx) {
            tmp = this.$texts[typ][text][this.$languageIdx]
            if (tmp.indexOf('~') > 0) {
              tmp = tmp.split('~')
              if (idx >= 0 && idx < tmp.length) {
                return (tmp[idx])
              }
            }
            return (this.$texts[typ][text][this.$languageIdx])
          }
        }
      }
      return ('$' + text)
    },
    toggleEditMode () {
    },
    saveTexts () {
      var data = JSON.parse(JSON.stringify(this.$texts))
      console.log(data)
      console.log(this.typ)
      var klasse = this.typ
      if (this.typ === undefined) {
        klasse = 'txt'
      }
      if (klasse in data) {
        console.log(data[klasse])
        if (this.tid in data[klasse]) {
          console.log(data[klasse][this.tid])
          data[klasse][this.tid] = this.tmpText
        } else {
          data[klasse][this.tid] = []
          data[klasse][this.tid].push(this.tmpText[0])
          data[klasse][this.tid].push(this.tmpText[1])
        }
        console.log(data)
        axios.post('/api/v1/System/updateTextFile', {text: data})
          .then(response => {
            console.log(response.data)
            // Vue.prototype.$texts = response.data
          })
          .catch(error => { console.log(error) })
      }
    }
  },
  mounted () {
    var tmp = this.$languageIdx
    this.$languageIdx = 0
    Vue.set(this.tmpText, 0, this.getText())
    this.$languageIdx = 1
    Vue.set(this.tmpText, 1, this.getText())
    this.$languageIdx = tmp
  }
}
</script>
<style scoped>
</style>


