<template>
  <div class="">
    <div class="tile is-horizontal">
      <table>
        <tr>
          <td><a class="button is-link is-small is-rounded">1</a></td>
          <td colspan="2"><txt tid="resetMask"/></td>
          <td><button class="button is-warning" @click="resetMask(); points=[]"><i class="fas fa-undo"></i></button></td>
        </tr>
        <tr>
          <td><a class="button is-link is-small is-rounded">2</a></td>
          <td><txt tid="cropImage"/></td>
          <td class="progtd"><progress class="progress is-success" :value="points.length" max="2"></progress></td>
          <td><button class="button " :class="points.length > 1 ? 'is-success' : 'is-outlined'"><i class="fas fa-check"></i></button></td>
        </tr>
        <tr>
          <td><a class="button is-link is-small is-rounded">3</a></td>
          <td colspan="2"><txt tid="saveRoi"/></td>
          <td><button class="button is-link" :disabled="points.length!=2" @click="saveRect()"><i class="fas fa-save"></i></button></td>
        </tr>
        <tr>
          <td><a class="button is-link is-small is-rounded">4</a></td>
          <td colspan="2"><txt tid="testNewRoi"/></td>
          <td><button class="button is-link" @click="test()"><i class="fas fa-bolt"></i></button></td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import bus from '@/components/bus'
import toolMixin from '@/components/aiCore/toolMixin'
import txt from '@/components/txt'

export default {
  name: 'toolRectPicker',
  mixins: [toolMixin],
  props: ['activePlugin', 'pluginConfig'],
  components: {
    txt
  },
  data () {
    return {
      swgId: 1,
      dragFlag: false,
      dragStart: {x: 0, y: 0},
      dragPos: {x: 0, y: 0},
      mode: -1
    }
  },
  methods: {
    mouseEvent (clkInfo) {
      this.scale = clkInfo.xDiv / clkInfo.trueX
      console.log(clkInfo.state, clkInfo.id, this.mode)
      if (clkInfo.state === 'mouseup' && this.mode === 0) {
        this.points.push({'x': clkInfo.x, 'y': clkInfo.y})
        this.mode = -1
        // this.saveRect()
      }
      // console.log(clkInfo.id)
      if (clkInfo.state === 'mousedown') {
        if (clkInfo.id === '$BG') {
          this.mode = 0
          this.points = []
          this.points.push({'x': clkInfo.x, 'y': clkInfo.y})
          this.removeRect()
          this.insertRect(clkInfo.x, clkInfo.y, 0, 0)
        // } else {
        //  this.mode = 1
        //  this.dragElement(clkInfo)
        }
      }
      if (clkInfo.state === 'mousemove') {
        if (this.mode === 0) {
          this.adjustRect({'x': clkInfo.x, 'y': clkInfo.y})
        // } else {
        //  this.dragElement(clkInfo)
        }
      }
    },
    dragElement (clkInfo) {
      var target = document.getElementById('ROI_' + this.svgId)
      if (target !== null) {
        var xo = target.getAttribute('x')
        var yo = target.getAttribute('y')
        // console.log(xo, yo)
        if (!this.dragFlag) {
          this.dragStart.x = clkInfo.x
          this.dragStart.y = clkInfo.y
          this.dragFlag = true
        }
        this.dragPos.x = clkInfo.x
        this.dragPos.y = clkInfo.y
        var x = (this.dragPos.x - this.dragStart.x) * this.scale
        var y = (this.dragPos.y - this.dragStart.y) * this.scale
        // console.log(x, y)
        target.setAttribute('x', parseFloat(xo) + parseFloat(x))
        target.setAttribute('y', parseFloat(yo) + parseFloat(y))
        target.setAttribute('stroke', 'red')
        this.dragStart.x = clkInfo.x
        this.dragStart.y = clkInfo.y
      }
    },
    removeRect () {
      var target = document.getElementById('ROI_' + this.svgId)
      // console.log(target)
      if (target !== null) {
        target.remove()
      }
    },
    insertRect (x, y, w, h) {
      this.rect(this.svgId,
            x * this.scale,
            y * this.scale,
            w * this.scale,
            h * this.scale,
            'yellow')
    },
    adjustRect (pos) {
      var s = this.scale
      var target = document.getElementById('ROI_' + this.svgId)
      // console.log(pos)
      if (pos.x > this.points[0].x) {
        target.setAttribute('width', (pos.x - this.points[0].x) * s)
      } else {
        target.setAttribute('x', pos.x * s)
        target.setAttribute('width', (this.points[0].x - pos.x) * s)
      }
      if (pos.y > this.points[0].y) {
        target.setAttribute('height', (pos.y - this.points[0].y) * s)
      } else {
        target.setAttribute('y', pos.y * s)
        target.setAttribute('height', (this.points[0].y - pos.y) * s)
      }
    },
    resetMask () {
      this.pluginConfig.topLeftX.value = 0
      this.pluginConfig.topLeftY.value = 0
      this.pluginConfig.btmRightX.value = 0
      this.pluginConfig.btmRightY.value = 0
      axios.post('/api/v1/' + this.activePlugin + '/saveConfig', this.pluginConfig)
        .then(response => {
          console.log(response)
          axios.post('/api/v1/' + this.activePlugin + '/start', {})
            .then(response => {
              console.log(response)
              this.clearCanvas()
            })
        })
        .catch(error => { console.log(error) })
    },
    saveRect () {
      var target = document.getElementById('ROI_' + this.svgId)
      var x = parseFloat(target.getAttribute('x')) / this.scale
      var y = parseFloat(target.getAttribute('y')) / this.scale
      var w = parseFloat(target.getAttribute('width')) / this.scale
      var h = parseFloat(target.getAttribute('height')) / this.scale
      // console.log(x, y, w, h)
      this.pluginConfig.topLeftX.value = parseInt(x)
      this.pluginConfig.topLeftY.value = parseInt(y)
      this.pluginConfig.btmRightX.value = parseInt(x + w)
      this.pluginConfig.btmRightY.value = parseInt(y + h)

      console.log(this.pluginConfig)
      axios.post('/api/v1/' + this.activePlugin + '/saveConfig', this.pluginConfig)
        .then(response => {
          console.log(response)
        })
        .catch(error => { console.log(error) })
    },
    test () {
      axios.post('/api/v1/' + this.activePlugin + '/start', {})
        .then(response => {
          console.log(response)
          this.clearCanvas()
        })
        .catch(error => { console.log(error) })
    }
  },
  mounted: function () {
    this.svgId = 1
    bus.$on('mouseEvent', clkInfo => { this.mouseEvent(clkInfo) })
  },
  beforeDestroy: function () {
    bus.$off('mouseEvent')
  }
}
</script>
<style scoped>
@import  '~vue-context/dist/css/vue-context.css'; /* ASK LUKE */
.tool {
  font-size: 1.0rem;
}
.progress {
  margin-top:4px;
}
.progtd {
  width:150px;
}

</style>



