<template>
  <div class="">
    <div v-if="activeProject">
      <div class="testButtons" v-for="(btn, idx) in activeProject.commands" :key="idx">
        <button @click="sendCmd('CMD=' + btn.program)"  class="button is-medium navBarBottomButton">{{btn.name}}</button>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
// NOT IN USE AT THE MOMENT
export default {
  name: 'toolTestButtons',
  components: {
  },
  props: ['activePlugin', 'pluginConfig', 'activeProject'],
  data () {
    return {
    }
  },
  methods: {
    sendCmd (cmd) {
      axios
        .get('/api/v1/Project/sendCmd?' + cmd)
        .then(function (response) {
          console.log(response.data)
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  mounted: function () {
  },
  beforeDestroy: function () {
  }
}
</script>
<style scoped>
@import  '~vue-context/dist/css/vue-context.css';
.tool {
  font-size: 1.0rem;
}
.listItem {
  background-color: aliceblue;
  font-size: 1rem;
}
.listItem.is-selected {
  background-color: steelblue;
}
.testButtons {
  padding:5px;
}
</style>



