<template>
  <div>
  </div>
</template>
<script>
import Vue from 'vue'
import bus from '@/components/bus'

export default {
  name: 'mqtt',
  props: ['subscriptionFilter'],
  data () {
    return {
      message: '',
      messages: [],
      plugins: []
    }
  },
  mqtt: {
    'logger/#': function (msg) {
      this.message = msg.toString()
      this.notifyActivePlugin(this.message)
      this.messages.unshift(this.message)
      Vue.set(this.messages, 0, this.message)
      if (this.messages.length > 3) {
        this.messages.pop()
      }
      // this.messages.reverse()
      // console.log(this.message)
    },
    'status/#': function (msg) {
      var status = msg.toString().replace(/'/g, '"')
      var obj = JSON.parse(status)
      // console.log(obj)
      bus.$emit('statusUpdate', obj)
    }
  },
  methods: {
    notifyActivePlugin: function (message) {
      var ma = message.split('@')
      if (ma.length > 2) {
        if (ma[2] === '$Start' || ma[2] === '$Finished') {
          // this.plugins[ma[1]] = ''
          // if (ma.length > 2) {
          // this.plugins[ma[1]] = ma[3]
          // }
          bus.$emit('pluginStatusUpdate', ma[1], ma[2], ma[3])
        } else if (ma[2].includes('$Error')) {
          bus.$emit('errorMessage', ma[1] + ' ' + ma[2])
          // console.log(';' + ma[2])
          // console.log('i emitted an error')
        } else if (ma[2].includes('$Info')) {
          // this.plugins[ma[1]] = Date.now()
          bus.$emit('infoMessage', ma[2])
          // console.log(';' + ma[2])
          // console.log('i emitted an error')
        }
      }
    }
  },
  mounted () { // TODO ONYL SUBSCRIBE TO MESSAGES FOR THIS PROJECT
    // Subscribe to mqtt messages
    this.$mqtt.subscribe('logger/' + this.subscriptionFilter + '/#')
    this.$mqtt.subscribe('status/' + this.subscriptionFilter + '/#')
  }
}
</script>
<style scoped>
#mqttTable {
  font-size: 0.8rem;
}
</style>


