<template>
  <div id="sidebarConfigMainWrapper">
    <div class="tabs is-centered">
      <ul>
        <li v-bind:class="{ 'is-active': sidebarTab == 'toolbox' }"><a @click="sidebarTab='toolbox'"><txt typ="menu" tid="ToolBox"/></a></li>
        <li v-bind:class="{ 'is-active': sidebarTab == 'config' }"><a @click="sidebarTab='config'"><txt typ="menu" tid="Config"/></a></li>
        <li v-bind:class="{ 'is-active': sidebarTab == 'info' }"><a @click="sidebarTab='info'"><txt typ="menu" tid="Info"/></a></li>
        <li v-bind:class="{ 'is-active': sidebarTab == 'pluginLibrary' }"><a @click="sidebarTab='pluginLibrary'"><txt typ="menu" tid="Library"/></a></li>
      </ul>
    </div>
    <div id="sidebarTabDisplay">
      <div id="sidebarLibrary" v-show="sidebarTab == 'pluginLibrary'">
        <div>
          <ul id="pluginLibraryList" class="list">
            <li v-for="(plugin, index) in pluginsList" :key="index" class="list-item">
              {{plugin}} <span v-if="isActive(plugin)"><i class="fas fa-check"></i></span>
            </li>
          </ul>
        </div>
      </div>
      <div id="sidebarToolBox" v-show="sidebarTab == 'toolbox'">
        <div id="sidebarToolBoxContent">
          <sidebarToolbox
            :activePlugin="activePlugin"
            :activeProject="activeProject"
            :pluginConfig="pluginConfig"
            :activePart="activePart"
            :targets="targets">
          </sidebarToolbox>
        </div>
      </div>
      <div id="sidebarConfig" v-show="sidebarTab == 'config'">
        <div class="">
          <div class="">
            <table v-if="pluginConfig" class="table is-striped is-hoverable is-fullwidth is-narrow" id="configTable">
              <tr v-for="(setting, index) in pluginConfig" :key="index">
                <td v-if="setting.type == 1">
                  <txt typ="var" :tid="index"/>
                </td>
                <td v-if="setting.type == 1">
                  <div class="control has-icons-right">
                    <div class="select" v-if="setting.select">
                      <select class="input" :type="setting.valueType" v-model="setting.value">
                        <option v-for="(option, index) in setting.select.split('~')" :key="index">{{option}}</option>
                      </select>
                    </div>
                    <input v-else class="input" :type="setting.valueType" v-model="setting.value"><span class="icon is-right">{{setting.units}}</span>
                  </div>
                </td>
              </tr>
            </table>
            <button class="button is-success" @click="updateConfig()" id="updateConfigButton"><span class="icon"><i class="fas fa-save"></i></span></button>
            <button class="button is-info" @click="testConfig()"><span class="icon"><i class="fas fa-sync-alt"></i></span></button>
            <button class="button is-danger" @click="resetConfig()" disabled><span class="icon"><i class="fas fa-trash-alt"></i></span></button>
          </div>
        </div>
      </div>
      <div id="sidebarDocu" v-show="sidebarTab == 'info'">
        <div id="sidebarDocuContent" v-if="pluginReadme.description">
          <span v-html="pluginReadme.description"></span><hr>
          <span v-html="pluginReadme.variables"></span><hr>
          <span v-html="pluginReadme.returns"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import bus from '@/components/bus'
import sidebarToolbox from '@/components/aiCore/sidebarToolbox'
import txt from '@/components/txt'

export default {
  name: 'sidebarconfig',
  components: {
    sidebarToolbox,
    txt
  },
  props: ['activePlugin', 'targets', 'activeProject', 'activePart', 'targets'],
  data () {
    return {
      pluginConfig: {},
      pluginReadme: {},
      pluginsList: [],
      sidebarTab: 'toolbox'
    }
  },
  watch: {
    activePlugin: function () {
      this.readpluginConfig()
      this.readpluginReadMe()
    }
  },
  methods: {
    isActive (plugin) {
      for (var i = 0; i < this.activeProject.plugins.length; i++) {
        if (plugin === this.activeProject.plugins[i].name) {
          return (true)
        }
      }
      return (false)
    },
    readPluginList () {
      if (this.activePlugin !== '') {
        axios.get('/api/v1/Project/listPlugins')
          .then(response => {
            // console.log(response.data)
            this.pluginsList = response.data
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    readpluginConfig () {
      if (this.activePlugin !== '') {
        axios.get('/api/v1/' + this.activePlugin + '/getConfig')
          .then(response => {
            // console.log(response.data)
            this.pluginConfig = response.data
            this.updatePluginList()
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    updatePluginList () {
      // console.log(this.activeProject)
      // console.log(this.pluginConfig)
      this.pluginConfig.frameIn.select = 'default'
      for (var i = 0; i < this.activeProject.plugins.length - 1; i++) {
        if (this.activeProject.plugins[i].name !== this.activePlugin) {
          this.pluginConfig.frameIn.select += '~' + this.activeProject.plugins[i].name
        }
      }
    },
    readpluginReadMe () {
      if (this.activePlugin !== '') {
        axios.get('/api/v1/' + this.activePlugin + '/getReadme')
          .then(response => {
            // console.log(response.data)
            this.pluginReadme = response.data
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    updateConfig () {
      var btn = document.getElementById('updateConfigButton')
      btn.classList.toggle('is-loading')
      setTimeout(function () {
        btn.classList.toggle('is-loading')
      }, 1000)
      // console.log(this.pluginConfig)
      for (var o in this.pluginConfig) {
        if (o.type === 1 && o.valueType === 'number') {
          if (o.value.indexOf('.') >= 0 || o.value.indexOf(',') >= 0) {
            o.value = parseFloat(o.value)
          } else {
            o.value = parseInt(o.value)
          }
        }
      }
      axios.post('/api/v1/' + this.activePlugin + '/saveConfig', this.pluginConfig)
        .then(response => {
          console.log(response)
        })
        .catch(error => {
          console.log(error)
        })
      bus.$emit('infoMessage', 'Pluginkonfiguration gespeichert')
    },
    testConfig () {
      var data = {}
      axios.post('/api/v1/' + this.activePlugin + '/start', data)
        .then(response => {
          console.log(response)
        })
        .catch(error => {
          console.log(error)
        })
    },
    capitalize (s) {
      if (typeof s !== 'string') return ''
      return s.charAt(0).toUpperCase() + s.slice(1)
    }
  },
  mounted: function () {
    this.readpluginConfig()
    this.readpluginReadMe()
    var self = this
    setTimeout(function () {
      self.readPluginList()
    }, 200)
    bus.$on('pluginStatusUpdate', function (pin, status) {
      if (self.activePlugin === pin && status === '$Finished') {  // TODO read targets as well
        self.readpluginConfig()
        self.readpluginReadMe()
      }
    })
    bus.$on('setSidebarTabConfig', sidebarTab => {
      this.sidebarTab = 'config'
    })
  },
  beforeDestroy: function () {
    bus.$off('pluginStatusUpdate')
  }
}
</script>
<style scoped>
.control.has-icons-right .icon.is-right {
  right: 10%;
}
#sidebarConfig {
  background: #ffffff;
  font-size: 1rem;
  height: 100%;
}
#sidebarConfigMainWrapper {
  /* 88vh when Accordion is closed */
  overflow-x: hidden;
  overflow-y: hidden;
}
#sidebarTabDisplay {
  overflow-x: hidden;
  overflow-y: scroll;
  height: 75vh;
}
#sidebarDocu {
  font-size: 1rem;
  text-align: left;
  height: 100%;
}
#sidebarDocuContent {
  height: 60vh;
  padding: 12px;
}
#sidebarToolBox {
}
#sidebarToolBoxContent {
}
#configTable {
  width: 100%;
}
::-webkit-scrollbar {
    width: 6px;
}
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 3px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    height: 20px;
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
#sidebarLibrary {
  font-size:0.85em;
}
</style>


