<template>
    <div class="columns is-gapless" v-if="startedFlag">
        <div id="progressBar" class="column is-12">
            <div id="sideBarTop">
                <router-link to="/plugins">
                <div class="logo"><span class="applyAILogo">applyAI Vision</span>
                </div>
                </router-link>
            </div>          
            <div class="tile is-child" style="margin-top: 10px;"></div>
            <button class="cssaccordion" id="accordionToggleButton" @click="arrowDown = !arrowDown; toggleAccordion();">
                <table class="">
                <tr>
                    <td>{{activeProject.projectName}} / {{activePart.number}}</td>
                    <td>{{totalCycleTime.toFixed(0)}} ms</td>
                    <td>
                    <i class="fas fa-chevron-down"></i>
                    </td>
                    <td v-if="runningMode==1"><button class="button is-warning is-small" v-on:click.stop @click="sendTestCmd()"><i class="fas fa-bolt"></i></button></td>
                </tr>
                </table>
            </button>
            <div id="accordionPanel" class="panel">
                <router-link to="/plugins">
                <!--div class="logo">
                    <txt typ="menu" tid="Active Part: "/>{{activeProject.part}} / {{activeProject.order}}
                </div-->
                    <table class="table is-narrow progressbarTable">
                    <draggable v-model="activeProject.plugins" @start="drag=true" @end="drag=false" tag="tbody">
                        <tr v-for="(plugins, index) in activeProject.plugins" :key="index" :id="'listElement_' + plugins.name" class="listItem" :class="{ isActive: activePlugin == plugins.name }" @click="activatePlugin(plugins.name)">
                        <td v-if="runningMode == 1"><button class="button is-small" @click="setPluginConfiguration()"><i class="fas fa-wrench"></i></button></td>
                        <td><span :id="plugins.name + 'button'" class="pluginSpans"><txt :tid="plugins.name"/></span></td>
                        <td>{{cycleTimes[index]}} ms</td>
                        </tr>
                    </draggable>
                    <!--tr class="listItem" v-for="(plugins, index) in activeProject.plugins" :key="index" :id="'listElement_' + plugins.name" :class="{ isActive: activePlugin == plugins.name }" @click="activatePlugin(plugins.name)">
                        <td><span :id="plugins.name + 'button'" class="pluginSpans"><txt :tid="plugins.name"/></span></td>
                        <td>{{cycleTimes[index]}} ms</td>
                    </tr-->
                    <tr><td>Total</td><td>{{totalCycleTime.toFixed(0)}} ms</td></tr>
                    </table>
                </router-link>
            </div>
            <sidebarConfig v-if="runningMode === 0"
                :activePlugin="activePlugin"
                :activeProject="activeProject"
                :activePart="activePart"
                :targets="targets">
            </sidebarConfig>
            <pluginViewer id="routerView" v-if="runningMode === 1"
              :configData="configData"
              :cfList="cfList"
              :stereoCameras="stereoCameras"
              :activePlugin="activePlugin"
              :activeProject="activeProject"
              :activePart="activePart"
              :pluginConfig="pluginConfig"
              :targets="targets">
            </pluginViewer>
            <sidebarMeasuredValues v-if="runningMode === 1" :targets="targets"></sidebarMeasuredValues>
        </div>
    </div>    
</template>
<script>
/* eslint-disable */
import bus from '@/components/bus'
import sidebarConfig from '@/components/aiCore/sidebarConfig'
import txt from '@/components/txt'
import axios from 'axios'
import draggable from 'vuedraggable'
import pluginViewer from '@/components/aiCore/pluginViewer'
import sidebarMeasuredValues from '@/components/aiCore/sidebarMeasuredValues'

export default {
  name: 'sidebarWrapper',
  props: [
    'activeProject',
    'activePlugin',
    'pluginConfig',
    'targets',
    'startedFlag',
    'activePart',
    'totalCycleTime',
    'runningMode',
    'cycleTimes',
    'cfList',
    'configData',
    'stereoCameras'
  ],
  components: {
      draggable,
      sidebarConfig,
      txt,
      pluginViewer,
      sidebarMeasuredValues
  },
  data () {
    return {
      arrowDown: true
    }
  },
  methods: {
    activatePlugin (name) {
      bus.$emit('changeActivePlugin', name)
    },
    toggleAccordion () {
      var e = document.getElementById('accordionToggleButton')
      e.classList.toggle('active')
      var panel = document.getElementById('accordionPanel')
      var child = document.getElementById('sidebarTabDisplay')
      if (this.runningMode == 1 && panel.style.maxHeight) {
        panel.style.maxHeight = null
      } else if (this.runningMode == 1) {
        panel.style.maxHeight = panel.scrollHeight + 'px'
      } else if (this.runningMode == 0 && panel.style.maxHeight) {
        panel.style.maxHeight = null
        child.style.height = 75 + 'vh'
      } else if (this.runningMode == 0) {
        panel.style.maxHeight = panel.scrollHeight + 'px'
        child.style.height = 45 + 'vh'
      }
    },
    sendTestCmd () {
      axios.get('/api/v1/Project/sendCmd?CMD=detect')
        .then(response => {
          bus.$emit('measuredValues', response)
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  mounted: function () {
  }
}
</script>
<style scoped>
@font-face {
  font-family: CenturyGothic;
  src: url('../../assets/fonts/CenturyGothic.ttf');
}
</style>


