
<template>
  <div>
    <div id="tables">
      <div id="statusBits">
        <table v-if="status" class="table is-fullwidth is-narrow">
          <tr>
            <th>Status</th>
            <th></th>
          </tr>
          <tr>
            <td>Moving</td>
            <td>{{status.bMoving}}</td>
          </tr>
          <tr>
            <td>Positon reached</td>
            <td>{{status.bPosValid}}</td>
          </tr>
          <tr>
            <td>Command active</td>
            <td>{{status.bCMD_Running}}</td>
          </tr>
          <tr>
            <td>Base Position</td>
            <td>{{status.bGS_Valid}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'roboCTLStatusTable',
  props: ['status'],
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
  },
  mounted () {
  }
}
</script>
<style scoped>
table th:nth-child(2) { text-align: right; }
table th:nth-child(3) { text-align: right; }
table td:nth-child(2) { text-align: right; }
table td:nth-child(3) { text-align: right; }


</style>


