<template>
  <div id="app" class="columns is-12 is-gapless">
    <div class="column is-4">
      <sidebarWrapper
        :activePlugin="activePlugin"
        :activeProject="activeProject"
        :activePart="activePart"
        :targets="targets"
        :startedFlag="startedFlag"
        :cycleTimes="cycleTimes"
        :totalCycleTime="totalCycleTime"
        :runningMode="runningMode"
        :pluginConfig="pluginConfig"
        :configData="configData"
        :cfList="cfList"
        :stereoCameras="stereoCameras">
      </sidebarWrapper>
    </div>
    <div class="column is-8">
      <navbarTop
        :activeProject="activeProject"
        :showParts="showParts"
        :showCharts="showCharts"
        :showOrders="showOrders"
        :showTargets="showTargets"
        :runningMode="runningMode">
      </navbarTop>
      <div id="routerViewWrapper">
        <div id="routerViewSubWrapper">
          <!--listTargets :targets="targets" v-if="showTargets" id="targetsDiv"></listTargets-->
          <pluginViewer id="routerView" v-if="runningMode === 0 && showCharts == false"
            :configData="configData"
            :cfList="cfList"
            :stereoCameras="stereoCameras"
            :activePlugin="activePlugin"
            :activeProject="activeProject"
            :activePart="activePart"
            :pluginConfig="pluginConfig"
            :targets="targets">
          </pluginViewer>
          <chartContainer v-if="showCharts" :activePart="activePart"></chartContainer>
          <router-view id="routerViewInfo"
            :configData="configData"
            :cfList="cfList"
            :stereoCameras="stereoCameras"
            :activePlugin="activePlugin"
            :activeProject="activeProject"
            :activePart="activePart"
            :pluginConfig="pluginConfig"
            :targets="targets">
          </router-view>
        </div>
      </div>
      <div class="column has-text-centered">
        <trackerContainer id="" :activeProject="activeProject" :activePart="activePart"></trackerContainer>
      </div>
    </div>
    <!--notifications
      :showNotification="showNotification"
      :showInformation="showInformation">
    </notifications-->
    <notificationDisplay></notificationDisplay>
    <mqtt v-if="activeProject.projectName" :subscriptionFilter="activeProject.projectName"></mqtt>
  </div>
</template>

<script>
import Vue from 'vue'
import bus from '@/components/bus'
import mqtt from '@/components/mqtt'
// import Vue from 'vue'

import axios from 'axios'
import sidebarWrapper from '@/components/aiCore/sidebarWrapper'
import sidebarConfig from '@/components/aiCore/sidebarConfig'
import progressBar from '@/components/aiCore/sidebarProgressBar'
import txt from '@/components/txt'
import pluginViewer from '@/components/aiCore/pluginViewer'
import navbarTop from '@/components/aiCore/navbarTop'
// import notifications from '@/components/aiCore/notifications'
import notificationDisplay from '@/components/aiCore/notificationDisplay'
import listTargets from '@/components/aiCore/listTargets'
import trackerContainer from '@/components/aiCore/trackerContainer'
import chartContainer from '@/components/aiCore/chartContainer'

export default {
  name: 'app',
  components: {
    mqtt,
    sidebarConfig,
    progressBar,
    txt,
    pluginViewer,
    sidebarWrapper,
    navbarTop,
    // notifications,
    notificationDisplay,
    listTargets,
    trackerContainer,
    chartContainer
  },
  data () {
    return {
      startedFlag: false,
      showTargets: false,
      targetsHTML: '',
      targets: {},
      showOrders: false,
      showParts: false,
      showCharts: false,
      showFrameIn: false,
      // showNotification: '',
      // showInformation: '',
      maxPlugins: 20,
      startTimes: [],
      cycleTimes: [],
      totalCycleTime: 0,
      activeProject: {},
      activePlugin: '',
      activeView: 'frame',
      activePart: {},
      pluginConfig: {},
      systemCfg: {},
      language: 'EN',
      runningMode: 0,
      toggleSidebar: true,
      toggleSidebarConfig: true,
      systemConfigFileName: 'systemConfig.json', // name of the systemconfig file in fixed path ../config
      configData: {},
      cfList: [],
      stereoCameras: true,
      randomNumber: 0,
      event: false,
      window: {
        width: 0,
        height: 0
      }
    }
  },
  watch: {
    activePlugin: function () {
      this.loadTargets()
      document.title = 'applyAI ' + this.activeProject.projectName
    },
    runningMode: function () {
      if (this.runningMode === 1) {
        this.showCharts = true
      } else if (this.runningMode === 0) {
        this.showCharts = false
      }
    }
  },
  methods: {
    setPluginConfiguration () {
      bus.$emit('setSidebarTabConfig')
    },
    getRunningMode () {
      axios.get('/api/v1/Project/getRunningMode')
        .then(response => {
          this.runningMode = response.data.runningMode
        })
    },
    handleProjectClick () {
      var self = this
      setTimeout(function () {
        // self.$router.push('/plugins')
        self.activatePlugin(self.activePlugin)
        // setTimeout(function () {
        //  bus.$emit('reLoadImage')
        // }, 20)
      }, 20)
    },
    loadTargets () {
      axios.get('/api/v1/' + this.activePlugin + '/getTargets')
        .then(response => {
          this.targets = response.data
          /* for (var i = 0; i < this.targets.length; i++) {
            for (var key in this.targets[i]) {
              console.log(this.targets[i][key])
              if (!isNaN(this.targets[i][key])) {
                if (key === 'angle') {
                  this.targets[i][key] = (180 / Math.PI * this.targets[i][key]).toFixed(2)
                } else if (key !== 'id') {
                  this.targets[i][key] = this.targets[i][key].toFixed(2)
                }
              }
            }
          } */
          // console.log(this.targets)
        })
        .catch(error => { console.log(error) })
    },
    activatePlugin (name) {
      this.activePlugin = name
      // console.log(name)
      axios.get('/api/v1/' + this.activePlugin + '/getConfig')
        .then(response => {
          // console.log(response.data)
          this.pluginConfig = response.data
        })
        .catch(error => { console.log(error) })
    },
    readProject () {
      axios.get('/api/v1/Project/getConfig')
        .then(response => {
          this.activeProject = response.data
          console.log(this.activeProject)
          // if no active plugin set then use the last one
          this.activePlugin = this.activeProject.plugins[this.activeProject.plugins.length - 1].name
        })
        .catch(error => { console.log(error) })
    },
    readOrder () {
      axios.get('/api/v1/Project/getOrder?extid=0000')
        .then(response => {
          console.log(response.data)
          var part = response.data.part
          axios.get('/api/v1/Project/getPart?number=' + part + '&version=1')
            .then(response => {
              console.log(response.data)
              this.activePart = response.data
              Vue.set(this.activePart, 'number', response.data.number)
            })
            .catch(error => { console.log(error) })
        })
        .catch(error => { console.log(error) })
    },
    readSystemCfg () {
      axios.get('/api/v1/System/getConfig')
        .then(response => {
          this.systemCfg = response.data
          this.language = this.systemCfg.language
          Vue.prototype.$languageIdx = 0
          if (this.language === 'DE') {
            Vue.prototype.$languageIdx = 1
          }
          console.log(this.systemCfg)
          this.startedFlag = true
        })
        .catch(error => { console.log(error) })
    },
    readTexts (cb) {
      axios.get('/api/v1/System/getTextFile')
        .then(response => {
          console.log(response.data.text)
          Vue.prototype.$texts = response.data.text
          cb()
        })
        .catch(error => { console.log(error) })
    },
    cameraSetupSwitch () {
      if (this.configData.application[1].value === '0') {
        this.stereoCameras = false
      } else if (this.configData.application[1].value === '1') {
        this.stereoCameras = true
      }
    },
    openTab (evt, tabName) {
      setTimeout(function () {
        bus.$emit('activateTab', tabName)
        console.log('inApp: ' + tabName)
      }, 100)
    },
    openModal (mdl) {
      var modal = document.getElementById(mdl)
      modal.classList.toggle('isActive')
    },
    sendCmd (cmd) {
      axios
        .get('/api/v1/Project/sendCmd?' + cmd)
        .then(function (response) {
          console.log(response.data)
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    sendCameraCmd (command) {
      bus.$emit('cam_from_hmi_web', command)
    },
    mountedProgressbar () {
      for (var i = 0; i < this.maxPlugins; i++) {
        this.startTimes[i] = 0
        this.cycleTimes[i] = 0
      }
      var self = this
      bus.$on('pluginStatusUpdate', function (pin, status, zeit) {
        var e = document.getElementById('listElement_' + pin)
        // console.log(pin, status, e)
        if (status === '$Start') {
        //  for (var i = 0; i < self.activeProject.plugins.length; i++) {
            // console.log(self.activeProject.plugins[i].name, pin)
        //    if (self.activeProject.plugins[i].name === pin) {
        //      self.startTimes[i] = Date.now()
        //      break
        //    }
        // }
          e.classList.add('active')
        }
        if (status === '$Finished') {
          if (self.activePlugin === pin) {
            self.loadTargets()
          }
          self.totalCycleTime = 0
          for (i = 0; i < self.activeProject.plugins.length; i++) {
            if (self.activeProject.plugins[i].name === pin) {
              self.$set(self.cycleTimes, i, zeit)
            }
            self.totalCycleTime += parseFloat(self.cycleTimes[i])
          }
          setTimeout(function () {
            e.classList.remove('active')
          }, 250)
        }
      })
    },
    displayErrorNotification () {
      // var self = this
      bus.$on('errorMessage', ma => {
        console.log('this is the errormessage:' + ma)
      })
    },
    handleResize () {
      this.window.width = window.innerWidth
      this.window.height = window.innerHeight
    },
    loadImageToMainDiv () {
    }
  },
  created () {
    window.addEventListener('resize', this.handleResize)
    this.handleResize()
  },
  destroyed () {
    window.removeEventListener('resize', this.handleResize)
  },
  mounted () {
    var self = this
    this.readTexts(function () {
      self.readSystemCfg()
      self.readProject()
      self.readOrder()
      self.mountedProgressbar()
      bus.$on('hw_from_robi_status', function (data) {
        self.status = data
        // console.log(self.status)
      })
      /* bus.$on('errorMessage', error => {
        self.showNotification = error
      }) */
      bus.$on('setRunningMode', mode => {
        self.runningMode = mode
        // if (mode === 1) {
        //   self.showCharts = true
        // } else if (mode === 0) {
        //   self.showCharts = false
        // }
        // JUST FOR TESTING THE CHANGE IN RUNNINGMODE
        // axios.get('/api/v1/Project/sendCmd?CMD=detect')
        //   .then(response => {
        //   })
        //   .catch(function (error) {
        //     console.log(error)
        //   })
        location.reload()
      })
      bus.$on('changeActivePlugin', name => {
        self.activatePlugin(name)
      })
      bus.$on('nokMeasurement', d => {
        // bus.$emit('loadImageFromTracker', d)  // TODO Lukas - This cannot be correct
        self.loadImageToMainDiv()
      })
      /* bus.$on('showInformation', msg => {
        self.showInformation = msg
      }) */
      /* bus.$on('showNotification', msg => {
        self.showNotification = msg
      }) */
      /* bus.$on('infoMessage', info => {
        self.showInformation = info.replace('$Info - ', '')
        setTimeout(() => { self.showInformation = '' }, 2000)
      }) */
      bus.$on('updateTargets', targets => {
        self.targets = targets
      })
      bus.$on('toggleTargets', toggleTargets => {
        self.showTargets = !self.showTargets
      })
      bus.$on('showCharts', function (n) {
        if (n === 0) {
          self.showCharts = false
        }
        if (n === 1) {
          self.showCharts = true
        }
      })
      bus.$on('togglePartsModal', showParts => {
        self.showParts = !self.showParts
      })
      bus.$on('toggleOrdersModal', showOrders => {
        self.showOrders = !self.showOrders
      })
      bus.$on('updatePart', data => {
        var part = data.number // + '~' + parseInt(data.version)
        axios.post('/api/v1/Project/savePart?name=' + part + '&version=' + data.version, data)
          .then(response => {
            self.activePart = data
            console.log(response)
          })
          .catch(error => { console.log(error) })
      })
    })
    this.getRunningMode()
    console.log('runningMode is: ' + this.runningMode)
    console.log('in init')
    // Get all "navbar-burger" elements
    const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0)
    console.log($navbarBurgers)
    // Check if there are any navbar burgers
    if ($navbarBurgers.length > 0) {
      // Add a click event on each of them
      $navbarBurgers.forEach(el => {
        el.addEventListener('click', () => {
          // Get the target from the "data-target" attribute
          const target = el.dataset.target
          const $target = document.getElementById(target)
          // Toggle the "is-active" class on both the "navbar-burger" and the "navbar-menu"
          el.classList.toggle('is-active')
          $target.classList.toggle('is-active')
        })
      })
    }
  }
}
</script>

<style>
@font-face {
  font-family: CenturyGothic;
  src: url('assets/fonts/CenturyGothic.ttf');
}
html, body {
  font-family: CenturyGothic;
  max-height: 100vh;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
#app {
  font-family: CenturyGothic;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: black;
  font-size: 1.2rem
}
#sideBarTop {
  height: 5vh;
}
#navbarVision {
  height: 5vh;
}
.button:hover {
  color: white;
  background-color: steelblue;
}
.applyAILogo {
  color: steelblue;
  font-family: CenturyGothic;
  font-size: 1.3rem;
}
#routerViewWrapper {
  min-height: 90%;
}
.sideBarTopDropdownMenus {
}
.sideBarTopDropdown {
  font-family: CenturyGothic;
  background-color: steelblue;
  color: white;
}
.dropdown-item {
  font-family: CenturyGothic;
  text-align: left;
}
.dropdown-item:hover {
  background-color: steelblue;
  color: white;
  cursor: pointer;
}
.dropdown-item.disabledItem {
  color: grey;
  background-color: whitesmoke;
  cursor: not-allowed;

}
#imgOverlayWrapper {
  height: 100%;
  position: absolute;
  top: 55px;
  right: 10px;
  z-index: 10;
  color: white;
  background-color: steelblue;
  overflow-y: auto;
  width: 50%;
}
#imgOverlay {
}
#imgOverlay table tr td {
  padding:5px;
}
.logo {
  font-size: 1.2rem;
  color:black;
}
.undertitle {
  font-size: 1rem;
}
table {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}
.progressbarTable tr > td:last-of-type, .progressbarTable tr > th:last-of-type {
  text-align: right;
  white-space: nowrap;
}
.router-link-active {
  background-color: steelblue;
  color: white;
}
.listItem.isActive {
  background-color: steelblue;
  color: white;
}
.listItem {
  background-color: rgb(186, 208, 229);
  font-size: 1rem;
}
.navbar {
  z-index: 0.5;
}
hr {
  background: steelblue;
}
#notificationDivWrapper, #informationDivWrapper {
  position: absolute;
  bottom: 7%;
  left: 5%;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
#routerViewSubWrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
#routerView {
  position: relative;
}
#routerViewInfo {
  position: absolute;
  top: 60px;
  left: 30px;
  background-color: aliceblue;
  opacity: 0.96;
}
.button.is-link {
  background-color: steelblue;
}
.button.is-link:hover {
  background-color: rgba(0,0,0,0.7);
}
tr.listItem:hover {
  background-color: rgba(186, 208, 229, 0.8);
}
#targetsDiv {
}

/* CSS Accordion */ 
/* Style the buttons that are used to open and close the accordion panel */
.cssaccordion {
  font-family: CenturyGothic;
  font-size: 20px;
  background-color: steelblue;
  color: white;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  transition: 0.4s;
}
/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active, .cssaccordion:hover {
  background-color: rgba(86, 136, 182, 0.8);
}

/* Style the accordion panel. Note: hidden by default */
.panel {
  padding: 0;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease-out;
}
.panel:not(:last-child) {
  margin-bottom: 0rem;
}
/* Cursos classes */
.context-menuCursor {cursor: context-menu;}
.col-resizeCursor {cursor: col-resize;}
.copyCursor {cursor: copy;}
.crosshairCursor {cursor: crosshair;}
.defaultCursor {cursor: default;}
.e-resizeCursor {cursor: e-resize;}
.ew-resizeCursor {cursor: ew-resize;}
.grabCursor {cursor: grab;}
.grabbingCursor {cursor: grabbing;}
.helpCursor {cursor: help;}
.moveCursor {cursor: move;}
.n-resizeCursor {cursor: n-resize;}
.ne-resizeCursor {cursor: ne-resize;}
.nesw-resizeCursor {cursor: nesw-resize;}
.ns-resizeCursor {cursor: ns-resize;}
.nw-resizeCursor {cursor: nw-resize;}
.nwse-resizeCursor {cursor: nwse-resize;}
.no-dropCursor {cursor: no-drop;}
.noneCursor {cursor: none;}
.not-allowedCursor {cursor: not-allowed;}
.pointerCursor {cursor: pointer;}
.progressCursor {cursor: progress;}
.row-resizeCursor {cursor: row-resize;}
.s-resizeCursor {cursor: s-resize;}
.se-resizeCursor {cursor: se-resize;}
.sw-resizeCursor {cursor: sw-resize;}
.textCursor {cursor: text;}
.w-resizeCursor {cursor: w-resize;}
.waitCursor {cursor: wait;}
.zoom-inCursor {cursor: zoom-in;}
.zoom-outCursor {cursor: zoom-out;}
</style>


