<template>
  <div id="listTargets">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Objektliste</p>
        <button class="delete is-pulled-right" aria-label="close" @click="$router.push('/plugins');closeTargetsModal()"></button>
      </header>
      <section class="modal-card-body">
        <b-table
          id="targetsTable"
          :data='targetArray'
          :bordered='isBordered'
          :striped='isStriped'
          :narrowed='isNarrowed'
          :hoverable='isHoverable'
          :loading='isLoading'
          :focusable='isFocusable'
          :mobile-cards='hasMobileCards'
          :paginated="isPaginated"
          :per-page="perPage"
          :current-page.sync="currentPage"
          :pagination-simple="isPaginationSimple"
          :pagination-position="paginationPosition"
          :default-sort-direction="defaultSortDirection"
          :sort-icon="sortIcon"
          :sort-icon-size="sortIconSize"
          default-sort="user.first_name"
          aria-next-label="Next page"
          aria-previous-label="Previous page"
          aria-page-label="Page"
          aria-current-label="Current page">
          <template slot-scope='props'>
            <!--b-table-column field='plugin'  label='Plugin' width='40' numeric>{{ props.row.id }}</b-table-column>
            <b-table-column field='id'      label='ID'>{{ props.row.id }}</b-table-column-->
            <b-table-column sortable field='type'    label='Type'>{{ props.row.type }}</b-table-column>
            <b-table-column sortable field='cx'      label='cx' numeric>{{ parseFloat(props.row.cx).toFixed(3) }}</b-table-column>
            <b-table-column sortable field='cy'      label='cy' numeric>{{ parseFloat(props.row.cy).toFixed(3) }}</b-table-column>
            <b-table-column sortable field='w'       label='Breite' numeric>{{ parseFloat(props.row.w).toFixed(3) }}</b-table-column>
            <b-table-column sortable field='h'       label='Hhe' numeric>{{ parseFloat(props.row.h).toFixed(3) }}</b-table-column>
            <b-table-column sortable field='angle'   label='Winkel' numeric>{{ parseFloat(props.row.angle).toFixed(3) }}</b-table-column>
            <b-table-column sortable field='dim'     label='Dimension' numeric>{{ parseFloat(props.row.dim).toFixed(3) }}</b-table-column>
          </template>
          <template slot='empty'>
            <section class='section'>
              <div class='content has-text-grey has-text-centered'>
                <p>
                  <b-icon icon='emoticon-sad' size='is-large'></b-icon>
                </p>
                <p>Nothing here.</p>
              </div>
            </section>
          </template>
        </b-table>
      </section>
    </div>
  </div>
</template>

<script>
import bus from '@/components/bus'

export default {
  name: 'listTargets',
  props: ['activePart', 'targets'],
  components: {
  },
  data () {
    return {
      isEmpty: false,
      isBordered: false,
      isStriped: true,
      isNarrowed: true,
      isHoverable: true,
      isFocusable: false,
      isLoading: false,
      hasMobileCards: false,
      isPaginated: true,
      isPaginationSimple: true,
      paginationPosition: 'bottom',
      defaultSortDirection: 'asc',
      sortIcon: 'arrow-up',
      sortIconSize: 'is-small',
      currentPage: 1,
      perPage: 10,
      targetArray: [
        [
          {'type': 'TEST'}
        ]
      ]
    }
  },
  methods: {
    closeTargetsModal () {
      bus.$emit('toggleTargets')
    }
  },
  mounted: function () {
    setTimeout(() => {
      this.targetArray = this.targets
    }, 200)
  }
}
</script>
<style>
.modal-card {
  min-width: 700px;
}
#targetsTable {
  font-size: 0.9rem;
}
</style>


