import Vue from 'vue'
import App from './App'
import router from './router'
import VueMqtt from 'vue-mqtt'
// import fs from 'fs'
import Buefy from 'buefy'
import 'buefy/dist/buefy.css'

import './../node_modules/bulma/css/bulma.css'
// import './../node_modules/bulma-extensions/bulma-switch/dist/css/bulma-switch.min.css'
import './../node_modules/bulma-extensions/dist/css/bulma-extensions.min.css'
// import bulmaAccordion from '/node_modules/bulma-extensions/bulma-calendar/dist/bulma-accordion.min.js'

var options = {
  port: 1884,
  protocolId: 'MQTT',
  rejectUnauthorized: true
  // key: fs.readFileSync('/home/lukas/certs/CA.key'),
  // cert: fs.readFileSync('/home/lukas/certs/CA.crt')
}

Vue.use(Buefy, {
  defaultIconPack: 'fas'
})
Vue.use(VueMqtt, 'ws://' + document.location.hostname, options)
// console.log(document.location.host)

Vue.config.productionTip = false
Vue.prototype.$texts = {}
Vue.prototype.$languageIdx = 0

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



