<template>
  <div>
    <div class="">
      <header class="modal-card-head">
        <p class="modal-card-title">Prfauftrge</p>
        <button class="delete is-pulled-right" aria-label="close" @click="$router.push('/plugins');closeOrdersModal()"></button>
      </header>
      <section class="modal-card-body">
        <b-table
          :data='data'
          :bordered='isBordered'
          :striped='isStriped'
          :narrowed='isNarrowed'
          :hoverable='isHoverable'
          :loading='isLoading'
          :focusable='isFocusable'
          :mobile-cards='hasMobileCards'
          :paginated="isPaginated"
          :per-page="perPage"
          :current-page.sync="currentPage"
          :pagination-simple="isPaginationSimple"
          :pagination-position="paginationPosition"
          :default-sort-direction="defaultSortDirection"
          :sort-icon="sortIcon"
          :sort-icon-size="sortIconSize"
          default-sort="user.first_name"
          aria-next-label="Next page"
          aria-previous-label="Previous page"
          aria-page-label="Page"
          aria-current-label="Current page">
          <template slot-scope='props'>
            <b-table-column field='activate'    label=''><button class="button is-small" :class="{'is-success': props.row.status}" @click="activate(props.row.id)"><i class="fas fa-check"></i></button></b-table-column>
            <b-table-column field='edit'    label=' '><button class="button is-small modal-button" @click="edit(props.row.id)"><i class="fas fa-edit"></i></button></b-table-column>
            <b-table-column sortable field='updated'    label='#'>{{ props.row.id}}</b-table-column>
            <b-table-column sortable field='extid'    label='ExtId'>{{ props.row.extid}}</b-table-column>
            <b-table-column sortable field='part'      label='Werkstck'>{{ props.row.part }}</b-table-column>
            <b-table-column sortable field='order'      label='Kommision' >{{ props.row.order }}</b-table-column>
            <b-table-column sortable field='machine'      label='Maschine' >{{ props.row.machine }}</b-table-column>
            <b-table-column sortable field='status'      label='Status' ><txt tid="order-status-select" :idx="props.row.status"/></b-table-column>
            <b-table-column sortable field='remove'      label='' centered><button class="button is-danger is-small" @click="remove(props.row)"><i class="fas fa-trash"></i></button></b-table-column>
          </template>
          <template slot='empty'>
            <section class='section'>
              <div class='content has-text-grey has-text-centered'>
                <p>
                  <b-icon icon='emoticon-sad' size='is-large'></b-icon>
                </p>
                <p>Nothing here.</p>
              </div>
            </section>
          </template>
        </b-table>
      </section>
    </div>
    <!--div class="column">
      <div class="row">
        <span class="is-pulled-left" ><txt typ="menu" tid="inspectionOrders"/></span>
        <button class="delete is-pulled-right" aria-label="close" @click="$router.push('/plugins');closeOrdersModal()"></button>
      </div>
      <table class="table is-narrow is-fullwidth is-hoverable">
        <tr>
          <th></th>
          <th></th>
          <th>#</th>
          <th><txt tid="extid"/></th>
          <th><txt tid="part"/></th>
          <th><txt tid="order"/></th>
          <th><txt tid="machine"/></th>
          <th><txt tid="status"/></th>
          <th></th>
        </tr>
        <tr v-for="p in data" :key="p.id" class="listItem" :id="'ordersList_' + p.id" @click="selectOrder(p)">
          <td><button class="button is-small" :class="{'is-success': p.status}" @click="activate(p.id)"><i class="fas fa-check"></i></button></td>
          <td><button class="button is-small modal-button" @click="edit(p.id)"><i class="fas fa-edit"></i></button></td>
          <td>{{p.id}}</td>
          <td>{{p.extid}}</td>
          <td>{{p.part}}</td>
          <td>{{p.order}}</td>
          <td>{{p.machine}}</td>
          <td><txt tid="order-status-select" :idx="p.status"/></td>
          <td><button class="button is-danger is-small" @click="remove(p.id)"><i class="fas fa-trash"></i></button></td>
        </tr>
      </table>
    </div-->
    <div id="modal-orders" class="modal" v-bind:class="{ 'is-active': isShowModal }">
      <div class="modal-background"></div>
      <div class="modal-card">
        <header class="modal-card-head">
          <p class="modal-card-title"><txt tid="order"/></p>
          <button class="delete" aria-label="close" @click="isShowModal = false"></button>
        </header>
        <section class="modal-card-body">
          <!-- Content ... -->
          <div class="field is-horizontal" v-for="(edt, name) in editData" :key="edt.id" :id="'edit_' + name">
            <div class="field-label is-normal">
              <label class="label"><txt :tid="name"/></label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control" v-if="name != 'status'">
                  <input class="input" v-model="editData[name]">
                </div>
                <div class="control" v-if="name == 'status'">
                  <div class="select">
                    <txtSelect tid="order-status-select" v-model="editData[name]"></txtSelect>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <footer class="modal-card-foot">
          <button class="button is-success" @click="save();isShowModal = false"><i class="fas fa-save"></i></button>
          <button class="button" @click="isShowModal = false"><i class="fas fa-window-close"></i></button>
        </footer>
      </div>
    </div>
  </div>
</template>
<script>
import bus from '@/components/bus'
import axios from 'axios'
import txt from '@/components/txt'
import txtSelect from '@/components/txtSelect'

export default {
  name: 'listOrders',
  props: [],
  components: {
    txt,
    txtSelect
  },
  data () {
    return {
      data: [],
      editData: {},
      dataLength: 0,
      total: 0,
      index: 0,
      countPerPage: 10,
      isShowModal: false,
      selected: {},
      isEmpty: false,
      isBordered: false,
      isStriped: true,
      isNarrowed: true,
      isHoverable: true,
      isFocusable: false,
      isLoading: false,
      hasMobileCards: false,
      isPaginated: true,
      isPaginationSimple: true,
      paginationPosition: 'bottom',
      defaultSortDirection: 'asc',
      sortIcon: 'arrow-up',
      sortIconSize: 'is-small',
      currentPage: 1,
      perPage: 10,
      targetArray: [
        [
          {'type': 'TEST'}
        ]
      ]
    }
  },
  methods: {
    edit (id) {
      console.log('in edit ' + id)
      for (var i = 0; i < this.data.length; i++) {
        if (id === this.data[i].id) {
          this.dataLength = JSON.stringify(this.data[i]).length
          console.log(this.dataLength)
          this.editData = this.data[i]
          this.editData.fill = ''
          console.log(this.editData)
          this.isShowModal = true
          break
        }
      }
    },
    activate (id) {
      console.log('in activate ' + id)
      for (var i = 0; i < this.data.length; i++) {
        if (id === this.data[i].id) {
          axios.get('/api/v1/Project/activateListElement?data=orders&id=' + id)
            .then(response => {
              console.log(response.data)
              this.getOrdersList()
            })
            .catch(error => { console.log(error) })
        }
      }
      bus.$emit('infoMessage', 'Prfauftrag aktiviert')
    },
    remove (id) {
      console.log('in remove ' + id)
      for (var i = 0; i < this.data.length; i++) {
        if (id === this.data[i].id) {
          axios.get('/api/v1/Project/deleteListElement?data=orders&id=' + id)
            .then(response => {
              console.log(response.data)
              this.getOrdersList()
            })
            .catch(error => { console.log(error) })
        }
      }
    },
    newOrder (task) {
      if (this.selected.part) {
        for (var i = 0; i < this.data.length; i++) {
          if (this.selected.id === this.data[i].id) {
            this.dataLength = JSON.stringify(this.data[i]).length
            console.log(this.dataLength)
            this.editData = this.data[i]
            this.editData.id = 9999999
            this.editData.fill = ''
            console.log(this.editData)
            this.isShowModal = true
            break
          }
        }
      } else {
        this.editData.id = 9999999
        this.editData.extid = '0000'
        this.editData.part = '---'
        this.editData.order = '---'
        this.editData.machine = '---'
        this.editData.status = 0
        this.editData.fill = ''
        this.isShowModal = true
      }
    },
    save () {
      this.isShowModal = false
      console.log(this.editData)
      axios.post('/api/v1/Project/saveListElement?data=orders&id=' + this.editData.id, this.editData)
        .then(response => {
          console.log(response.data)
          this.getOrdersList()
        })
        .catch(error => { console.log(error) })
      bus.$emit('infoMessage', 'Prfauftrag gespeichert')
    },
    close () {
      console.log('in close')
      this.isShowModal = false
    },
    page (page) {
      this.currentPage = page
      if (this.currentPage < 1) {
        this.currentPage = 1
      }
      if ((this.currentPage - 1) * this.countPerPage > this.total) {
        this.currentPage = parseInt(this.total / this.countPerPage) + 1
      }
      this.index = (this.currentPage - 1) * this.countPerPage
      this.getOrdersList()
    },
    getOrdersList () {
      axios.get('/api/v1/Project/getList?data=orders&index=' + this.index + '&count=' + this.countPerPage)
        .then(response => {
          this.total = response.data.total
          this.data = response.data.data
          // console.log(this.data)
        })
        .catch(error => { console.log(error) })
    },
    selectOrder (p) {
      for (var i = 0; i < this.data.length; i++) {
        document.getElementById('ordersList_' + this.data[i].id).classList.remove('is-selected')
        if (this.data[i].id === p.id) {
          document.getElementById('ordersList_' + this.data[i].id).classList.add('is-selected')
          this.selected = p
        }
      }
    },
    closeOrdersModal () {
      bus.$emit('toggleOrdersModal')
    }
  },
  mounted: function () {
    this.getOrdersList()
  }
}
</script>
<style scoped>
.listItem {
  background-color: aliceblue;
  font-size: 1rem;
}
.listItem.is-selected {
  background-color: steelblue;
}
#edit_fill {
  display: none;
  overflow: hidden;
}
#edit_id {
  display: none;
  overflow: hidden;
}
</style>


