<template>
  <div id="toolColorPicker">
    <article class="tile is-parent notification" @contextmenu.prevent="$refs.hsvSelectMenu.open">
      <p id="resultHSV" class="column is-4 is-centered">HSV<br>0 0% 0%</p>
      <p id="resultCOL" class="column is-4 is-centered"></p>
      <p id="resultRGB" class="column is-4 is-centered">#000000<br>(0,0,0)</p>
    </article>
    <vue-context ref="hsvSelectMenu">
      <txt tid="selectColorAs"/>
      <ul>
        <li @click="setHSVCfgVal('max')">maximum HSV</li>
        <li @click="setHSVCfgVal('min')">minimum HSV</li>
      </ul>
    </vue-context>
  </div>
</template>
<script>
import axios from 'axios'
import bus from '@/components/bus'
import { VueContext } from 'vue-context'
import txt from '@/components/txt'

export default {
  name: 'toolColorPicker',
  components: {
    VueContext,
    txt
  },
  props: ['activePlugin', 'pluginConfig'],
  data () {
    return {
      col: {}
    }
  },
  methods: {
    setHSVCfgVal (opt) {
      if (opt === 'max') {
        this.pluginConfig.upperH.value = parseInt(this.col.h) + 10
        if (this.pluginConfig.upperH.value > 360) {
          this.pluginConfig.upperH.value = 360
        }
        this.pluginConfig.upperS.value = parseInt(this.col.s * 100) + 10
        if (this.pluginConfig.upperS.value > 100) {
          this.pluginConfig.upperS.value = 100
        }
        this.pluginConfig.upperV.value = parseInt(this.col.v * 100) + 10
        if (this.pluginConfig.upperV.value > 100) {
          this.pluginConfig.upperV.value = 100
        }
      }
      if (opt === 'min') {
        this.pluginConfig.lowerH.value = parseInt(0)
        if (this.pluginConfig.lowerH.value < 0) {
          this.pluginConfig.lowerH.value = 0
        }
        this.pluginConfig.lowerS.value = parseInt(this.col.s * 100) - 10
        if (this.pluginConfig.lowerS.value < 0) {
          this.pluginConfig.lowerS.value = 0
        }
        this.pluginConfig.lowerV.value = parseInt(this.col.v * 100) - 10
        if (this.pluginConfig.lowerV.value < 0) {
          this.pluginConfig.lowerV.value = 0
        }
      }
    },
    pad (num, size) {
      var s = '000000000' + num
      return s.substr(s.length - size)
    },
    pixelInfo (clkInfo) {
      var data = {'plugin': this.activePlugin, 'frameIn': clkInfo.showFrameIn, 'channel': 0, 'x': clkInfo.x, 'y': clkInfo.y}
      axios.post('/api/v1/' + this.activePlugin + '/getPixelInfo', data)
        .then(response => {
          console.log(response.data)
          this.col = response.data
          var rgb = document.getElementById('resultRGB')
          var farbe = document.getElementById('resultCOL')
          var hsv = document.getElementById('resultHSV')
          var rgbStr = this.pad(parseInt(this.col.r).toString(16).toUpperCase(), 2) +
                        this.pad(parseInt(this.col.g).toString(16).toUpperCase(), 2) +
                        this.pad(parseInt(this.col.b).toString(16).toUpperCase(), 2)
          rgb.innerHTML = '#' + rgbStr + '<br>(' + this.col.r + ',' + this.col.g + ',' + this.col.b + ')'
          farbe.style.backgroundColor = '#' + rgbStr
          hsv.innerHTML = 'HSV:<br>' + parseFloat(this.col.h).toFixed(0) + ' ' + parseFloat(this.col.s * 100).toFixed(0) + '% ' + parseFloat(this.col.v * 100).toFixed(0) + '%'
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  mounted: function () {
    var self = this
    bus.$on('mouseEvent', function (clkInfo) {
      console.log(clkInfo)
      // if (self.activePlugin === 'Color') {
      self.pixelInfo(clkInfo)
      // }
    })
  },
  beforeDestroy: function () {
    bus.$off('mouseEvent')
  }
}
</script>
<style scoped>
@import  '~vue-context/dist/css/vue-context.css';
#toolColorPicker {
  font-size: 1.0rem;
}
#resultCOL {
   width:33%;
   height:40px;
   border: 3px white solid;
}
#resultHSV, #resultRGB {
  font-size: 0.9rem;
  padding: 3px;
}
</style>



