<template>
  <div class="">
    Messungen
    <br>
    <button class="button is-info modal-button" :class="{activeButton: measureDisplay == 1}" @click="setMeasureDisplay(1);measureProgress=0;measurementSaved=false">Neue Messung</button> 
    <button class="button is-info modal-button" :class="{activeButton: measureDisplay == 0}" @click="setMeasureDisplay(0)">Liste Messungen</button> 
    <br>
    <div id="measureTypeListContainer" v-if="measureDisplay == 1">
      <table class="table is-hoverable">
        <tr id="measureToolTableRow" v-for="(tool,idx) in tools" :key="idx" @click="selectMTool(tool);setMeasureDisplay(2)">
          <td>
            <button :class="'button ' + selectedMTool(tool)"
              :disabled="tool.disabled"
              :data-tooltip="tool.tooltip">
              <img width="20" height="20" :src="'/static/svg/' + tool.name + '.svg'">
            </button>
          </td>
          <td>
            {{tool.tooltip}}
          </td>
        </tr>
      </table>
      <!--span class="toollist" v-for="(tool,idx) in tools" :key="idx">
        {{tool}}
        <button :class="'button ' + selectedMTool(tool)"
          :disabled="tool.disabled"
          :data-tooltip="tool.tooltip"
          @click="selectMTool(tool)">
          <img width="20" height="20" :src="'/static/svg/' + tool.name + '.svg'">
        </button>
      </span-->
      <!--span class="toollist">
        <button class="button" @click="insertMoi()"><i class="fas fa-file"></i></button>
      </span-->
    </div>
    <div id="measureTypeListContainer" v-if="measureDisplay == 2">
      <table class="table is-hoverable">
        <div class="tile is-horizontal">
          <table>
            <tr v-for="n in requiredPoints" :key="n">
              <td><a class="button is-link is-small is-rounded">{{n}}</a></td>
              <td colspan="2">{{n}}. Punkt setzen </td>
              <td><button class="button is-success" v-if="points.length >= n || measureProgress == 1"><i class="fas fa-check"></i></button></td>              
            </tr>
            <tr>
            <tr>
              <td><a class="button is-link is-small is-rounded">3</a></td>
              <td colspan="2"><txt tid="saveRoi"/></td>
              <td><button v-if="!measurementSaved" id="saveMeasurementButton" class="button is-success" :disabled="measureProgress !== 1" @click="saveMeasurement()"><i class="fas fa-save"></i></button><button v-if="measurementSaved" class="button is-success"><i class="fas fa-check"></i></button></td>              
            </tr>
            <tr>
              <td><a class="button is-link is-small is-rounded">4</a></td>
              <td colspan="2"><txt tid="testNewRoi"/></td>
              <td><button class="button is-link" @click="testMeasurement()"><i class="fas fa-bolt"></i></button></td>
            </tr>
          </table>
        </div>
      </table>
      <hr>
      <button class="button is-danger" @click="cancelMeasurement()" :disabled="measurementSaved == true">Abbrechen</button>
    </div>
    
    {{message}}<br>
    <br>
    <div class="measurementTool is-horizontal" v-if="measureDisplay == 0">
      <div class="columns" v-if="moi.length === 0">
        <div class="column is-12 has-text-centered">Keine Messungen gespeichert</div>
      </div>
      <table>
        <tr v-for="m in moi" :key="m.id" class="listItem" :id="'moiList_' + m.id" @click="selectMoi(m)">
          <!--td>{{m.id}}</td-->
          <!--td>{{m.nickname}}-->
            <!--input class="input" type="text" v-model="m.nickname" placeholder="nickname no blanks"-->
          <!--/td-->
          <!--td>
            <div class="select">
              <select class="input" type="text" v-model="m.type">
                <option v-for="(option, index) in tools" :key="index">{{option.name}}</option>
              </select>
            </div>
          </td-->
          <td id="measurementListSvgTd"><img width="20" height="20" :src="'/static/svg/' + m.path.split('~')[1] + '.svg'"></td>
          <!--td>{{m.path.split('~')[1]}}</td-->
          <td v-if="editMeasurementNames"><input class="input is-small measurementNicknameInput" type="text" v-model="m.nickname"></td>
          <td v-if="!editMeasurementNames">{{m.nickname}}</td>
          <td>{{m.path.split('~')[0]}}</td>
          <td>{{m.path.split('~')[2]}}</td>
          <td>{{m.path.split('~')[3]}}</td>
          <td>{{m.path.split('~')[4]}}</td>
          <td>{{m.path.split('~')[5]}}</td>
          <td><button class="button is-small" @click="deleteMoi(m.id)"><i class="fas fa-trash-alt"/></button></td>
        </tr>
      </table>
      <br>
      <button class="button is-info" @click="editMeasurementNames = !editMeasurementNames"><i class="fas fa-cog"></i></button>
      <button class="button is-success" :disabled="!editMeasurementNames" @click="saveMeasurement();editMeasurementNames = false"><i class="fas fa-save"></i></button>
        <!--tr>
          <td><a class="button is-link is-small is-rounded">1</a></td>
          <td colspan="2">Select P1</td>
          <td><button class="button is-small" :class="points.length > 0 ? 'is-success' : 'is-outlined'"><i class="fas fa-check"></i></button></td>
        </tr>
        <tr>
          <td><a class="button is-link is-small is-rounded">2</a></td>
          <td>Select P2</td>
          <td>D={{d2.toFixed(0)}}px</td>
          <td><button class="button is-small" :class="points.length > 1 ? 'is-success' : 'is-outlined'"><i class="fas fa-check"></i></button></td>
        </tr>
        <tr>
          <td><a class="button is-link is-small is-rounded">3</a></td>
          <td>Select P3</td>
          <td>D={{d3.toFixed(0)}}px</td>
          <td><button class="button is-small" :class="points.length > 2 ? 'is-success' : 'is-outlined'"><i class="fas fa-check"></i></button></td>
        </tr>
        <tr>
          <td><a class="button is-link is-small is-rounded">4</a></td>
          <td colspan="2">Reset measurement</td>
          <td><button class="button is-warning is-small" @click="reset()"><i class="fas fa-undo"></i></button></td>
        </tr-->
    </div>
  </div>
</template>
<script>
import bus from '@/components/bus'
import toolMixin from '@/components/aiCore/toolMixin'
import txt from '@/components/txt'
import axios from 'axios'
import sidebarMeasuredValues from '@/components/aiCore/sidebarMeasuredValues'

export default {
  name: 'toolMeasure',
  mixins: [toolMixin],
  props: ['activePlugin', 'pluginConfig', 'targets', 'activePart'],
  components: {
    txt,
    sidebarMeasuredValues
  },
  data () {
    return {
      editMeasurementNames: false,
      measurementSaved: false,
      measureProgress: 0,
      n: 0,
      requiredPoints: 0,
      measureDisplay: 0,
      isShowModal: false,
      scale: 1,
      activeTool: 'P2P',
      moiId: 0,
      moiIdSelected: 0,
      path: '',
      idStr: '',
      points: [],
      moi: [],
      message: '',
      tools: [
        {name: 'P2P', pts: 2, disabled: false, tooltip: 'Punkt zu Punkt', selected: false, cursor: ''},
        {name: 'L2P', pts: 3, disabled: false, tooltip: 'Linie zu Punkt', selected: false, cursor: ''},
        {name: 'L2L', pts: 4, disabled: false, tooltip: 'Linie zu Linie', selected: false, cursor: ''},
        {name: 'LLP', pts: 4, disabled: false, tooltip: 'Schnittpunkt Linie/Punkt', selected: false, cursor: ''},
        // {name: 'RAD', pts: 1, disabled: false, tooltip: 'Radius', selected: false, cursor: ''},
        {name: 'C2P', pts: 2, disabled: false, tooltip: 'Kreismittelpunkt zu Punkt', selected: false, cursor: ''},
        {name: 'C2L', pts: 3, disabled: false, tooltip: 'Kreismittelpunkt zu Linie', selected: false, cursor: ''},
        {name: 'C2C', pts: 2, disabled: false, tooltip: 'Kreismittelpunkt zu Kreismittelpunkt', selected: false, cursor: ''},
        {name: 'L2LM', pts: 4, disabled: false, tooltip: 'Mittellinie', selected: false, cursor: ''},
        {name: 'RAD', pts: 2, disabled: false, tooltip: 'Radius', selected: false, cursor: ''},
        {name: 'ANG', pts: 4, disabled: false, tooltip: 'Winkel', selected: false, cursor: ''}
      ]
    }
  },
  methods: {
    setMeasureDisplay (id) {
      this.measureDisplay = id
    },
    insertMoi (path) {
      this.moi.push({id: this.moiId, type: this.activeTool, nickname: 'MOI.' + this.moiId, path: path})
      this.moiId++
      this.points = []
      this.idStr = ''
      this.path = ''
    },
    updateMoi (moi) {
      var container = document.querySelector('#frameInOut')
      var matches = container.querySelectorAll('circle,line')
      matches.forEach(function (item) {
        if (moi.path.indexOf(item.id) < 0) {
          item.setAttribute('stroke', 'yellow')
          item.setAttribute('stroke-width', 1)
        }
      })
      matches.forEach(function (item) {
        if (moi.path.indexOf(item.id) >= 0) {
          item.setAttribute('stroke', 'aqua')
          item.setAttribute('stroke-width', 5)
        }
      })
    },
    deleteMoi (id) {
      for (var i = 0; i < this.moi.length; i++) {
        if (this.moi[i].id === id) {
          this.moi.splice(i, 1)
          this.reset('M.' + id)
          this.reset('MP.' + id)
          break
        }
      }
      bus.$emit('infoMessage', 'Messung: M' + id + ' gelscht')
    },
    selectMoi (m) {
      for (var i = 0; i < this.moi.length; i++) {
        document.getElementById('moiList_' + this.moi[i].id).classList.remove('is-selected')
        if (this.moi[i].id === m.id) {
          document.getElementById('moiList_' + this.moi[i].id).classList.add('is-selected')
          this.highlightMoi(this.moi[i])
        }
      }
    },
    highlightMoi (m) {
      console.log('highlightMoi')
      console.log(m)
      // for (var i = 0; i < this.moi.length; i++) {
      // this.updateMoi('M.' + this.moi[i].id, 'magenta')
      // this.updateMoi('MP.' + this.moi[i].id, 'magenta')
      // }
      this.updateMoi(m)
      // this.updateMoi('MP.' + m.id, 'lime')
    },
    selectMTool (tool) {
      for (var i = 0; i < this.tools.length; i++) {
        if (tool.name === this.tools[i].name) {
          this.tools[i].selected = true
          this.activeTool = this.tools[i].name
        } else {
          this.tools[i].selected = false
        }
      }
      this.measureDisplay = 1
      this.requiredPoints = this.getNoOfPtsRequired(this.activeTool)
    },
    selectedMTool (tool) {
      if (tool.name === this.activeTool) {
        return ('is-link')
      } else {
        return ('')
      }
    },
    buildName (clkInfo) {
      var sep = '~'
      this.idStr = 'M.' + this.moiId + '.' + this.points.length + sep + this.activeTool
      this.path += sep + clkInfo.id
      return (this.idStr + this.path)
    },
    mouseEvent (clkInfo) {
      var s = clkInfo.xDiv / clkInfo.trueX
      if (clkInfo.state === 'mouseup' && clkInfo.id !== '$BG') {
        this.points.push(...this.getPointsFromSVG(clkInfo.id))
        console.log(this.points)
        var path = this.buildName(clkInfo)
        var ptsReq = this.getNoOfPtsRequired(this.activeTool)
        this.message = 'point ' + this.points.length + '/' + ptsReq + ' saved'
        if (ptsReq >= this.points.length && this.points.length >= 2) {
          this.calcAndDrawSvgPoints(s, path, this.moiId, this.points, ptsReq)
          if (ptsReq === this.points.length) {
            this.insertMoi(path)
            this.message = 'finished - select tool'
            this.measureProgress = 1
            var self = this
            setTimeout(function () { self.message = '' }, 2000)
          }
        }
      }
    },
    calcAndDrawSvgPoints (s, path, moiId, points, ptsReq) {
      var col = 'yellow'
      var calc = path.split('~')[1]
      console.log(path)
      var m, a, b, c, d, x, y, x1, y1, x2, y2
      if (points.length === 1) {
        // this.arc('MP.' + moiId + '.1', points[0].x * s, points[0].y * s, 6, col)
      }
      if (points.length === 2) {
        if (calc !== 'L2LM') {
          this.line(path, points[0].x * s, points[0].y * s, points[1].x * s, points[1].y * s, col)
          // this.arc('MP.' + moiId + '.2', points[1].x * s, points[1].y * s, 6, col)
        }
      }
      if (points.length === 3 && ptsReq === 3) {
        m = (points[0].y - points[1].y) / (points[0].x - points[1].x)
        c = points[1].y - m * points[1].x
        // this.d3 = Math.abs(c + m * this.points[2].x - this.points[2].y) / (Math.sqrt(1 + m * m))
        // calculate P4 as intersection line P1-P2 and point P3
        x = (points[2].x + m * points[2].y - m * c) / (m ** 2 + 1)
        y = c + m * (points[2].x + m * points[2].y - m * c) / (m ** 2 + 1)
        this.arc('MP.' + moiId + '.4', x * s, y * s, 6, col)
        this.line(path, x * s, y * s, points[2].x * s, points[2].y * s, col)
      }
      if (points.length === 4 && calc === 'L2L') {
        this.line(path, points[0].x * s, points[0].y * s, points[1].x * s, points[1].y * s, col)
        this.line(path, points[2].x * s, points[2].y * s, points[3].x * s, points[3].y * s, col)
        x1 = (points[0].x + points[1].x) / 2
        y1 = (points[0].y + points[1].y) / 2
        x2 = (points[2].x + points[3].x) / 2
        y2 = (points[2].y + points[3].y) / 2
        this.arc('MP.' + moiId + '.5', x1 * s, y1 * s, 6, col)
        this.arc('MP.' + moiId + '.6', x2 * s, y2 * s, 6, col)
        this.line(path, x1 * s, y1 * s, x2 * s, y2 * s, col)
      }
      if (points.length === 4 && calc === 'L2LM') {
        // this.line(path, points[0].x * s, points[0].y * s, points[1].x * s, points[1].y * s, col)
        // this.line(path, points[2].x * s, points[2].y * s, points[3].x * s, points[3].y * s, col)
        x1 = (points[0].x + points[2].x) / 2
        y1 = (points[0].y + points[2].y) / 2
        x2 = (points[1].x + points[3].x) / 2
        y2 = (points[1].y + points[3].y) / 2
        this.arc('LP.' + (100 + parseInt(moiId)) + '.1', x1 * s, y1 * s, 2, col)
        this.arc('LP.' + (100 + parseInt(moiId)) + '.3', (x1 + x2) / 2 * s, (y1 + y2) / 2 * s, 4, col)
        this.arc('LP.' + (100 + parseInt(moiId)) + '.2', x2 * s, y2 * s, 2, col)
        this.line(path, x1 * s, y1 * s, x2 * s, y2 * s, col)
      }
      if (points.length === 4 && calc === 'LLP') {
        a = (points[0].y - points[1].y) / (points[0].x - points[1].x)
        b = (points[2].y - points[3].y) / (points[2].x - points[3].x)
        c = points[1].y - a * points[1].x
        d = points[3].y - b * points[3].x
        // calculate P4 as intersection line P1-P2 and line P3-P4
        x = (d - c) / (a - b)
        y = a * x + c
        console.log(a, b, c, d, x, y)
        this.arc('MP.' + moiId + '.5', x * s, y * s, 6, col)
        this.line(path, x * s, y * s, points[0].x * s, points[0].y * s, col)
        this.line(path, x * s, y * s, points[3].x * s, points[3].y * s, col)
      }
      if (points.length === 4 && calc === 'ANG') {
        a = (points[0].y - points[1].y) / (points[0].x - points[1].x)
        b = (points[2].y - points[3].y) / (points[2].x - points[3].x)
        c = points[1].y - a * points[1].x
        d = points[3].y - b * points[3].x
        // calculate P4 as intersection line P1-P2 and line P3-P4
        x = (d - c) / (a - b)
        y = a * x + c
        var ang1 = Math.atan(a) * 180.0 / Math.PI + 0.0
        var ang2 = Math.atan(b) * 180.0 / Math.PI + 0.0
        var step = 5; if (ang1 > ang2) { var tmp = ang1; ang1 = ang2; ang2 = tmp }
        // if (ang1 < 0 || ang2 < 0) { ang1 += 360.0; ang2 += 360.0 }
        console.log('Calculate Angle ' + moiId + ' ' + ang1 + ' ' + ang2)
        console.log(a, b, c, d, x, y)
        this.circlePath('MP.' + moiId + '.5', x * s, y * s, 30, col, 1, ang1, ang2, step, true)
        this.line(path, x * s, y * s, points[0].x * s, points[0].y * s, col)
        this.line(path, x * s, y * s, points[3].x * s, points[3].y * s, col)
      }
    },
    getNoOfPtsRequired (tool) {
      for (var i = 0; i < this.tools.length; i++) {
        if (tool === this.tools[i].name) {
          return (this.tools[i].pts)
        }
      }
      return (0)
    },
    getPointsFromSVG (id) {
      var s = this.scale
      var p = []
      var t = document.getElementById(id)
      if (t !== null) {
        p.push({x: parseFloat(t.getAttribute('cx')) / s, y: parseFloat(t.getAttribute('cy')) / s})
      }
      return (p)
    },
    insertSvg (path) {
      var s = this.scale
      var pts = []
      var p = path.split('~')
      var ptsReq = this.getNoOfPtsRequired(p[1])
      for (var i = 2; i < p.length; i++) {
        // var pt = p[i].split('.')
        var id = p[i] // 'LP.' + pt[1] + '.' + pt[2]
        if (id.indexOf('LP') === 0 || id.indexOf('MP') === 0 || id.indexOf('CP') === 0) {
          pts.push(this.getPointsFromSVG(id)[0])
          // console.log(ptsReq, pts.length)
          if (ptsReq >= pts.length && pts.length >= 2) {
            var idx = p[0].split('.')[1]
            this.calcAndDrawSvgPoints(s, path, idx, pts, ptsReq)
          }
        }
      }
    },
    saveMeasurement () {
      if (this.measureDisplay === 2) {
        var self = this
        var btn = document.getElementById('saveMeasurementButton')
        btn.classList.toggle('is-loading')
        setTimeout(function () {
          btn.classList.toggle('is-loading')
          self.measurementSaved = true
          this.measureDisplay = 0
        }, 1000)
      }
      this.activePart.moi = JSON.parse(JSON.stringify(this.moi))
      bus.$emit('updatePart', this.activePart)
      bus.$emit('infoMessage', 'Messung gespeichert')
    },
    reset (id) {
      this.path = ''
      this.points = []
      this.d3 = 0
      var container = document.querySelector('#frameInOut')
      var matches = container.querySelectorAll('circle,line')
      // console.log(matches)
      matches.forEach(function (item) {
        // console.log(item.id + '/' + id)
        if (item.id.indexOf(id) === 0) {
          container.removeChild(item)
        }
      })
    },
    testMeasurement () {
      axios.get('/api/v1/Project/sendCmd?CMD=detect')
        .then(response => {
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    cancelMeasurement () {
      console.log(this.points)
      if (this.points.length > 0) {
        this.points = []
      } else if (this.measureProgress === 1 && this.moi.length > 0) {
        console.log('IN ELSE')
        console.log(this.moi[this.moi.length - 1].id)
        this.deleteMoi(this.moi[this.moi.length - 1].id)
      }
      this.measureProgress = 0
      this.measureDisplay = 1
      this.measurementSaved = false
      bus.$emit('infoMessage', 'Konfiguration abgebrochen')
    }
  },
  mounted: function () {
    this.scale = document.getElementById('imageDiv').clientWidth / 2048
    var s = this.scale
    for (var i = 0; i < this.targets.length; i++) {
      var t = this.targets[i]
      var did = document.getElementById('L' + t.id)
      if (did !== null) {
        did.remove()
      }
      if (t.type === 'Line') {
        t.x1 = parseFloat(t.cx) - parseFloat(t.w) / 2 * Math.cos(parseFloat(t.angle))
        t.y1 = parseFloat(t.cy) - parseFloat(t.w) / 2 * Math.sin(parseFloat(t.angle))
        t.x2 = parseFloat(t.cx) + parseFloat(t.w) / 2 * Math.cos(parseFloat(t.angle))
        t.y2 = parseFloat(t.cy) + parseFloat(t.w) / 2 * Math.sin(parseFloat(t.angle))
        console.log(t)
        this.line('L.' + t.id, t.x1 * s, t.y1 * s, t.x2 * s, t.y2 * s, 'yellow')
        this.arc('LP.' + t.id + '.1', t.x1 * s, t.y1 * s, 2, 'yellow')
        this.arc('LP.' + t.id + '.2', t.x2 * s, t.y2 * s, 2, 'yellow')
        this.arc('LP.' + t.id + '.3', t.cx * s, t.cy * s, 4, 'yellow')
      }
      if (t.type === 'Circle') {
        var r = (parseFloat(t.h) + parseFloat(t.w)) / 4
        // this.arc('CP.' + t.id, t.cx * s, t.cy * s, s * r, 'lime')
        this.arc('CP.' + t.id, t.cx * s, t.cy * s, 6, 'yellow')
        this.circlePath('CP.' + t.id + '.1', t.cx * s, t.cy * s, s * r, 'yellow')
      }
    }
    this.moi = JSON.parse(JSON.stringify(this.activePart.moi))
    this.moiId = 0
    for (i = 0; i < this.moi.length; i++) {
      // console.log(this.moi[i].path)
      var id = parseInt(this.moi[i].path.split('~')[0].split('.')[1])
      this.insertSvg(this.moi[i].path)
      if (id >= this.moiId) {
        this.moiId = id + 1
        this.moiIdSelected = this.moiId
      }
      this.updateMoi(this.moi[i])
    }
    bus.$on('mouseEvent', clkInfo => { this.mouseEvent(clkInfo) })
  },
  beforeDestroy: function () {
    bus.$off('mouseEvent')
    this.activePart.moi = JSON.parse(JSON.stringify(this.moi))
    bus.$emit('updatePart', this.activePart)
    var container = document.querySelector('#frameInOut')
    // console.log(container)
    var matches = container.querySelectorAll('circle,line,path')
    matches.forEach(function (item) {
      container.removeChild(item)
    })
    // console.log(matches)
  }
}
</script>
<style scoped>
@font-face {
  font-family: CenturyGothic;
  src: url('../../assets/fonts/CenturyGothic.ttf');
}
.measurementTool {
  font-family: CenturyGothic;
}
.activeButton {
  box-shadow:20px 0 0;
}
.tool {
  font-size: 1.0rem;
}
table {
  margin-left: 5px;
  font-family: CenturyGothic;
  font-size: 1.1rem;
}
table td:last-of-type {
  text-align:right;
}
.listItem {
  background-color: aliceblue;
  font-size: 1rem;
}
