
<template>
  <div>
    <br>
    <h3>Robot Control</h3>
    <hr>
    <div id="tables">
      <div class="columns">
        <div class="column is-4 is-offset-2">
          <roboCTLPositionTable :status="status"></roboCTLPositionTable>
        </div>
        <div class="column is-4">
          <roboCTLStatusTable :status="status"></roboCTLStatusTable>
        </div>
      </div>
    </div>
    <hr>
    <!--button @click="sendCmd('robiCMD=0')"  class="button is-success">Grundst.  (00)</button>
    <button @click="sendCmd('robiCMD=1')"  class="button is-success">Pos. 1    (01)</button>
    <button @click="sendCmd('robiCMD=2')"  class="button is-success">Pos. 2    (02)</button>
    <button @click="sendCmd('robiCMD=3')"  class="button is-success">Pos. 3    (03)</button>
    <button @click="sendCmd('robiCMD=7')"  class="button is-success">GRF AUF   (07)</button>
    <button @click="sendCmd('robiCMD=8')"  class="button is-success">GRF ZU    (08)</button>
    <button @click="sendCmd('robiCMD=10')" class="button is-success">Automatic (10)</button>
    <button @click="sendCmd('robiCMD=11')" class="button is-success">Automatic (11)</button>
    <button @click="sendCmd('robiCMD=12')" class="button is-warning">Test (12)</button>
    <button @click="sendCmd('robiCMD=13')" class="button is-warning">Test (13)</button>
    <button @click="sendCmd('robiCMD=14')" class="button is-warning">Test (14)</button>
    <button @click="sendCmd('robiCMD=20')" class="button is-success">Teach XY  (20)</button>
    <button @click="sendCmd('robiCMD=21')" class="button is-success">Teach Z   (21)</button>
    <button @click="sendCmd('robiCMD=22')" class="button is-success">Teach Winkel (22)</button>
    <button @click="sendCmd('robiCMD=30')" class="button is-success">Einzelbild/Detect (30)</button>
    <button @click="sendCmd('robiCMD=31')" class="button is-success">Exp + (31)</button>
    <button @click="sendCmd('robiCMD=32')" class="button is-success">Exp - (32)</button>
    <button @click="sendCmd('robiCMD=99')" class="button is-success">CMD Exit  (99)</button-->
  </div>
</template>

<script>
import bus from '@/components/bus'
import roboCTLPositionTable from '@/components/roboCTLPositionTable'
import roboCTLStatusTable from '@/components/roboCTLStatusTable'

export default {
  name: 'roboCTL',
  props: ['status'],
  components: {
    roboCTLPositionTable,
    roboCTLStatusTable
  },
  data () {
    return {
    }
  },
  methods: {
    sendCmd (command) {
      bus.$emit('hw_from_hmi_web', command)
    }
  },
  mounted () {
  }
}
</script>
<style scoped>

</style>


