<template>
  <div>
    <div>
      <table id="logtarget" class="table"></table>
    </div>
    <br>
    <button class="button is-light" v-on:click="readLog(-1)"></button>
    <button class="button is-light" v-on:click="readLog(1)">></button>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  name: 'logs',
  data () {
    return {
      intervalID: -1,
      page: 1
    }
  },
  methods: {
    readLog (direction) {
      if (direction === 0) {
        this.page = 1
      }
      if (direction < 0) {
        this.page -= 1
        if (this.page < 1) {
          this.page = 1
        }
      }
      if (direction > 0) {
        this.page += 1
      }
      axios
        .get('/api/v1/logfiles?page=' + this.page)
        .then(function (response) {
          console.log(response.data)
          document.getElementById('logtarget').innerHTML = response.data
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    reloadLog () {
      var self = this
      // this.intervalID = setInterval(function () {
      self.readLog(0)
      // }, 1000)
    }
  },
  mounted: function () {
    this.reloadLog()
  }
}
</script>
<style scoped>
@import  '~vue-context/dist/css/vue-context.css';
table td {
  padding-top: 0px;
  padding-bottom: 0px
}
table {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  font-size: 0.9rem;
  text-align: left;
}
.table td, .table th {
  border: 1px solid #ddd;
  padding-top: 0px;
  padding-bottom: 0px
}

table tr:nth-child(even){background-color: #f2f2f2;}

table tr:hover {background-color: #ddd;}

#logtarget.tr.td {
  text-align: left;
}
</style>


