<template>
  <div id="imageAndTargetsWrapper">
    <div id="imgWrapper" v-if="activePlugin!=='Part'" @mousemove="showCoords($event)">
      <imageContainer :activePlugin="activePlugin" :activeProject="activeProject" :showFrameIn="showFrameIn"></imageContainer>
      <div id="coordsDisplayer" style="position: absolute; bottom: 0; z-index: 9999; background-color: white; opacity: .95; font-size:0.75em;" v-html="overElement"></div>
      <!--div id="trackerOverlay" v-if="activeProject.trackerIDsDisplayed">
        <trackerContainer :activeProject="activeProject" :activePart="activePart"></trackerContainer>
      </div-->
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import bus from '@/components/bus'
import imageContainer from '@/components/aiCore/imageContainer'
import chartContainer from '@/components/aiCore/chartContainer'
import trackerContainer from '@/components/aiCore/trackerContainer'
import txt from '@/components/txt'

export default {
  name: 'pluginViewer',
  props: ['activePlugin', 'pluginConfig', 'activeProject', 'targets', 'activePart', 'showCharts'],
  components: {
    imageContainer,
    chartContainer,
    trackerContainer,
    txt
  },
  data () {
    return {
      counter: 0,
      trueHeight: 0,
      trueWidth: 0,
      clientX: Number,
      clientY: Number,
      offsetX: Number,
      offsetY: Number,
      overElement: '',
      showFrameIn: true,
      msg: {}
    }
  },
  watch: {
  },
  methods: {
    triggerCamera () {
      axios.get('/api/v1/Camera/trigger?camera=0')
    },
    displayCoords () {
      var e = document.getElementById('coordsDisplayer')
      e.style.display = 'block'
    },
    hideCoords () {
      var e = document.getElementById('coordsDisplayer')
      e.style.display = 'none'
    },
    showCoords (event) {
      // console.log(event)
      this.clientX = event.clientX
      this.clientY = event.clientY
      this.offsetX = parseInt(this.trueWidth * event.offsetX / event.path[1].clientWidth) // * 100 / event.currentTarget.offsetWidth)
      this.offsetY = parseInt(this.trueHeight * event.offsetY / event.path[1].clientHeight) // * 100 / event.currentTarget.offsetHeight)
      this.overElement = this.findTargetInfo()
      var mouseFollower = document.getElementById('coordsDisplayer')
      if (this.overElement !== '') {
        // mouseFollower.style.top = this.clientY + -20 + 'px'
        // mouseFollower.style.left = this.clientX + 10 + 'px'
        mouseFollower.style.display = 'block'
      } else {
        mouseFollower.style.display = 'none'
      }
    },
    findTargetInfo () {
      // console.log(this.targets.length)
      for (var i = 0; i < this.targets.length; i++) {
        var r = Math.min(this.targets[i].w, this.targets[i].h) / 2
        if (Math.abs(this.targets[i].x - this.offsetX) < r && Math.abs(this.targets[i].y - this.offsetY) < r) {
          var info = JSON.stringify(this.targets[i]).replace(/"/g, '').replace(/{/g, '').replace(/}/g, '')// .replace(/,/g, '<br>')
          return (info)
        }
      }
      // return ('x: ' + this.offsetX + '<br>y: ' + this.offsetY)
      return ('')
    }
  },
  beforeUpdate () {
    this.counter++
    // console.log(this.counter)
  },
  mounted () {
    var self = this
    bus.$on('showFrameIn', function () {
      self.showFrameIn = !self.showFrameIn
    })
    bus.$on('imageTransformed', function (msg) {
      self.trueWidth = msg.x
      self.trueHeight = msg.y
      self.msg = msg
      // document.getElementById('imgWrapper').style.height = (msg.yDiv + 10) + 'px'
    })
  }
}
</script>
<style scoped>
#coordsDisplayer {
  display: none;
}
#imgChartOverlay {
  position: absolute;
  top: 40px;
  left: 10px;
  right: 10px;
  z-index: 10;
  opacity: 1.0;
}
#trackerOverlay {
  display: block;
  text-align: center;
  background-color: steelblue;
}
#imgWrapper {
  padding: 5px;
  position: relative;
  background-color: steelblue;
}
#imgBtnContainer {
  position: absolute;
  text-align: center;
  top: 5px;
  left: 5px;
  background-color: rgba(255,255,255,.7);
  padding-left: 2px;
}
img {
  image-rendering: pixelated;
}
.imageControlCheckbox {
  text-align: right;
}
.infoButton {
  background-color: steelblue;
}
[data-title] {
  outline: red dotted 1px; /*optional styling*/
  font-size: 30px; /*optional styling*/
  
  position: relative;
  cursor: help;
}

[data-title]:hover::before {
  content: attr(data-title);
  position: absolute;
  bottom: -26px;
  display: inline-block;
  padding: 3px 6px;
  border-radius: 2px;
  background: #000;
  color: #fff;
  font-size: 12px;
  font-family: sans-serif;
  white-space: nowrap;
}
[data-title]:hover::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 8px;
  display: inline-block;
  color: #fff;
  border: 8px solid transparent;	
  border-bottom: 8px solid #000;
}
</style>


